import React, {useState} from 'react'
import Footer from '../../components/home/Footer'
import { createMessage } from '../../functions/message'
import './Contact.css'
import {toast} from 'react-toastify'

const ContactUs = ()=>{
    const initial = {
        firstName:'',
        lastName:'',
        Subject:'',
        Message:''
    }
    const [message, setMessage] = useState(initial)

    const handleChange=(e)=>{
        setMessage({...message, [e.target.name] : e.target.value})
    }

    const handleClick=()=>{
        console.log(message)
        if(message.firstName !== ""){
            createMessage(message).then((res)=>{
                setMessage(initial)
                toast.success('Thank You for reaching BIGBERRY PHARMA...We will reach you within 24 hour!!')
            }).catch((err)=>{
                console.log(err.message)
                toast.error('Message not sent...Please try again!!!')
            })
        }else{
            toast.error('Please fill the form!!')
        }
    }
    return(
        <> 
        <div className="container-fluid main-body mb-5 px-0">
            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d227822.55035758688!2d80.80242651696383!3d26.84862299347323!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x399bfd991f32b16b%3A0x93ccba8909978be7!2sLucknow%2C%20Uttar%20Pradesh!5e0!3m2!1sen!2sin!4v1615624353453!5m2!1sen!2sin" className="map" loading="lazy"></iframe>
            <div className="container py-5 text-center">
                <h3 className="text-uppercase text-center">Contact Detail</h3>
                <div className="head-underline mx-auto"></div>
                <div className="row pt-5" data-aos="fade-up" data-aos-duration="1000">
                    <div className="col-12 mx-auto col-md-4 mb-4 mb-md-0">
                        <i className="fa fa-phone icon-con"></i>
                        <h5 className="pt-3">Call Us</h5>
                        <p>+91 7007407965</p>
                    </div>
                    <div className="col-12 mx-auto col-md-4 mb-4 mb-md-0">
                        <i className="fa fa-map icon-con"></i>
                        <h5 className="pt-3">Address</h5>
                        <p>New Delhi, India</p>
                    </div>
                    <div className="col-12 mx-auto col-md-4">
                        <i className="fa fa-envelope icon-con"></i>
                        <h5 className="pt-3">Email</h5>
                        <p>bigberrypharma@gmail.com</p>
                    </div>
                </div>
            </div>
            <div className="container pb-5 text-center">
                <h3 className="text-uppercase text-center">Leave Us A Message</h3>
                <div className="head-underline mx-auto"></div>
                <div className="col-10 py-5 mx-auto text-center" data-aos="fade-up" data-aos-duration="1000">
                    <div className="row message-form">
                        <div className="col-6 pl-0">
                            <input type="text" name="firstName" value={message.firstName} onChange={handleChange} placeholder="First Name"></input>
                        </div>
                        <div className="col-6 pr-0">
                            <input type="text" name="lastName" value={message.lastName} onChange={handleChange} placeholder="Last Name"></input>
                        </div>
                    </div>
                    <div className="row mt-4 message-form-sub">
                        <input type="text" name="Subject" value={message.Subject} onChange={handleChange} placeholder="Subject"></input>
                    </div>
                    <div className="row mt-4 message-form-msg">
                        <textarea name="Message" value={message.Message} onChange={handleChange} placeholder="Message"></textarea>
                    </div>
                    <button className="mt-3 msg-btn" onClick={handleClick}>Send Message</button>
                </div>
               
            </div>
        </div>
        <Footer/>
        </>
    )
}

export default ContactUs