import React, {useState} from 'react'
import axios from 'axios'
import Resizer from 'react-image-file-resizer'
import {Badge, Avatar} from 'antd'
import {useSelector} from 'react-redux'
import { ClockCircleOutlined} from '@ant-design/icons'

const FileUpload = ({values, setValues})=>{
    const [loading, setLoading] = useState(false)
    let Images = values.images
    const {user} = useSelector((state)=>({...state}))
    const {REACT_APP_API_URL} = process.env

    const handleFileResizeAndUpload = (e)=>{
        let files = e.target.files
        for(let i=0; i<files.length; i++){
            console.log(files[i])
            Resizer.imageFileResizer(files[i], 720, 720, "JPEG",100, 0, (uri)=>{
                axios.post(`${REACT_APP_API_URL}/uploadimages`, {image:uri}, 
                {headers:{authtoken:user.token}}).then((res)=>{
                    console.log(res.data)
                    Images.push(res.data)
                    setValues({...values, ['images']:Images})
                }).catch((err)=>{
                    console.log(err)
                })
            }, 'base64')
        }
    }

    const handleImageRemove=(image_id)=>{
        setLoading(true)
        axios.post(`${REACT_APP_API_URL}/removeImages`, {image_id}, {headers:{authtoken:user.token}}).then((res)=>{
            const {images} = values
            let filteredImage = images.filter((image)=>{
                return image.public_id !== image_id
            })
            setValues({...values, ['images']: filteredImage})
            setLoading(false)
        }).catch((err)=>{
            console.log(err)
            setLoading(false)
        })
    }
    return(
        <>
        <label className="btn btn-sm btn-outline-primary" >
            <input type="file"  multiple hidden accept="images/*" onChange={handleFileResizeAndUpload}/>Choose Images
        </label>
        <div className="my-3">
                {values.images && values.images.map((image)=>{
                    return(
                        <Badge count={loading ? <ClockCircleOutlined style={{ color: '#f5222d' }}/> : 'X'} key={image.public_id} onClick={()=>handleImageRemove(image.public_id)}>
                            <Avatar src={image.url} className="ml-3" size={80}/>
                        </Badge>
                    )
                })}
            </div>
        </>
    )
}

export default FileUpload