import React, {useState} from 'react'
import {auth} from '../firebase'
import {Button, Input, Modal} from 'antd'
import {toast} from 'react-toastify'

const RegisterForm = ({isModalVisible, setIsModalVisible})=>{
    const [email, setEmail] = useState('')

    const handleSubmit = async()=>{
        //SEND LINK TO EMAIL
        
        let config = {
            url:`${process.env.REACT_APP_REGISTER_COMPLETE_REDIRECT_URL}/register/complete`,
            handleCodeInApp:true
        }
        return await auth.sendSignInLinkToEmail(email, config).then((res)=>{
            localStorage.setItem('emailForRegistration', email)
            setEmail("")
            setIsModalVisible(false)
            toast.success(`Verification is sent on ${email}.`)
        }).catch((err)=>{
            toast.error('Email You eneterd was not correct!!')
        })
    }

    
    return(
        <>
        <Modal title="Register" footer={null} visible={isModalVisible} onCancel={()=>setIsModalVisible(false)}>
            <Input type="text" value={email} onChange={(e)=>setEmail(e.target.value)} className="form-control" placeholder="Enter Your Email"/>
            <Button disabled={!email.includes('@')} type="primary" shape="round" block className="mt-3" onClick={handleSubmit}>Submit</Button>
        </Modal>
        </>
    )
}

export default RegisterForm