import React, {useState} from 'react'
import {Input, Modal} from 'antd'
import StarRatings from 'react-star-ratings'

const RateModal = ({visible, setVisible, handleRating,comment, setComment, rating, submitRating})=>{
    return(
        <>
            <Modal visible={visible} onCancel={()=>setVisible(false)} onOk={submitRating} title="Leave Your Rating">
                <div className="text-center">
                    <StarRatings numberOfStars={5} starRatedColor="red" rating={rating} changeRating={handleRating}>
                    </StarRatings>
                    <Input type="text" className="mt-4" value={comment} onChange={(e)=>setComment(e.target.value)}></Input>
                </div>
                
            </Modal>
        </>
    )
}

export default RateModal