import { Button, Input } from 'antd'
import React, {useState, useEffect} from 'react'
import AdminNav from '../../components/nav/AdminNav'
import { createCoupon, listCoupons, removeCoupon } from '../../functions/coupon'
import {useSelector} from 'react-redux'
import {DeleteOutlined, EditOutlined} from '@ant-design/icons'
import {Link} from 'react-router-dom'
import { toast } from 'react-toastify'

const Coupon = ()=>{
    const [coupon, setCoupon] = useState([])
    const [title, setTitle] = useState('') 
    const [discountRate, setDiscountRate] = useState(0)
    const [expiry, setExpiry] = useState('')
    const {user} = useSelector(state => ({...state}))

    useEffect(()=>{
        loadAllCoupon()
    },[user])

    const loadAllCoupon = ()=>{
        listCoupons(user && user.token).then((res)=>{
            console.log(res.data)
            setCoupon(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }
    const handleSubmit=()=>{
        createCoupon(user.token, {title, discountRate, expiry}).then((res)=>{
            setTitle('')
            setDiscountRate(0)
            setExpiry('')
            loadAllCoupon(user && user.token)
            toast.success(`${res.data.title} is successfully created!!`)
        }).catch((err)=>{
            toast.error(`${title} is not created...Try Again!!`)
        })
    }

    const handleRemove = (slug)=>{
        removeCoupon(user.token, slug).then((res)=>{
            if(res.data.success){
                toast.success(`${slug} is successfully deleted!!`)
                loadAllCoupon()
            }
        }).catch((err)=>{
            toast.error(`${slug} is not deleted...Try Again!!`)
        })
    }
    return(
        <>
        <div className="container-fluid">
            <div className="row">
                <AdminNav/>
                <div className="col-10 mx-auto bg-gray dash-content text-center">
                    <h4 className="text-center pt-5 pb-3">Create Coupon</h4>
                    <Input type="text" placeholder="Category Name" value={title} onChange={(e)=>setTitle(e.target.value)} className="col-10 col-lg-6 mb-3"/><br/>
                    <Input type="number" placeholder="Discount Rate" value={discountRate} onChange={(e)=>setDiscountRate(e.target.value)} className="col-10 col-lg-6 mb-3"/><br/>
                    <Input type="date" placeholder="Expiry Date" value={expiry} onChange={(e)=>setExpiry(e.target.value)} className="col-10 col-lg-6"/><br/>
                    <Button type="primary" shape="round" block className="col-10 col-lg-6 my-3" onClick={handleSubmit}>Submit</Button>
                    <div className="col-12 col-lg-6 my-3 mx-auto">
                        {coupon && coupon.map((item)=>{
                            return(
                                <>
                                    <div className="alert alert-primary list_box" key={item._id}>{item.title}
                                        <span className="float-right text-danger">{<DeleteOutlined onClick={()=>handleRemove(item.slug)}/>}</span>
                                        <span className="float-right pr-2">{<Link to={`/admin/update/coupon/${item.slug}`}><EditOutlined className=" text-warning"/></Link>}</span>
                                    </div>
                                </>
                            )
                        })}
                    </div>
                </div>
            </div>  
        </div>
        </>
    )
}
export default Coupon