import React, {useState, useEffect} from 'react'
import {useSelector} from 'react-redux'
import AdminNav from '../../components/nav/AdminNav'
import {Input, Select, Button} from 'antd'
import { readProduct, updateProduct } from '../../functions/product'
import { listCategories, listSubOnCategory } from '../../functions/category'
import FileUpload from '../../components/FileUpload'
import {toast} from 'react-toastify'

const UpdateProduct = ({match, history})=>{
    const [product, setProduct] = useState({})
    const [categories, setCategories] = useState([])
    const [selectedCategory, setSelectedCategory] = useState('')
    const [subsOption, setSubsOption] = useState([])
    const [arrOfSubsId, setArrOfSubsId] = useState([])
    const {Option} = Select
    const {user} = useSelector((state)=>({...state}))
    let {title, description, price, category,slug, quantity, shipping} = product
    useEffect(()=>{
        getProduct()
        loadAllCategories()
    }, [])

    const getSubOption = (id)=>{
        listSubOnCategory(id).then((res)=>{
            setSubsOption(res.data)
        })
    }

    const getProduct = ()=>{
        readProduct(match.params.slug).then((res)=>{
            setProduct(res.data)
            setSelectedCategory(res.data.category)
            getSubOption(res.data.category)
            let arr = []
            res.data.subs.map((item)=>{
                arr.push(item._id)
            })
            setArrOfSubsId(arr)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const loadAllCategories = ()=>{
        listCategories().then((res)=>{
            setCategories(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleChange=(e)=>{
        setProduct({...product, [e.target.name]: e.target.value})
    }
    
    const handleCategoryChange = (value)=>{
        if(value == category){
            getProduct()
        }
       setSelectedCategory(value)
       getSubOption(value)
       setArrOfSubsId([])
    }

    const handleSubCategoryChange = (value)=>{
        setArrOfSubsId(value)
    }

    const handleSubmit = ()=>{
        product.subs = arrOfSubsId
        product.category = selectedCategory ? selectedCategory : category
        updateProduct(user.token, slug, product).then((res)=>{
            toast.success(`${res.data.title} is successfully updated!!`)
            history.push('/admin/product')
        }).catch((err)=>{
            console.log(err)
        })
    }
    return(
        <>
        <div className="container-fluid">
            <div className="row">
                <AdminNav/>
                <div className="col-10 mx-auto bg-gray dash-content text-center">
                    <h4 className="text-center pt-5 pb-3">Update Product</h4>
                    <FileUpload values={product} setValues={setProduct}/>
                    <Input type="text" placeholder="Product Name" value={title} onChange={handleChange} name='title' className="col-10 col-lg-6 mb-3"/><br/>
                    <Input type="text" placeholder="Product description" value={description} onChange={handleChange} name='description' className="col-10 col-lg-6 mb-3"/><br/>
                    <Input type="number" placeholder="Product Price" value={price} onChange={handleChange} name='price' className="col-10 col-lg-6 mb-3"/><br/>
                    <Input type="number" placeholder="Product Quantity" value={quantity} onChange={handleChange} name='quantity' className="col-10 col-lg-6 mb-3"/><br/>
                    <Select  placeholder="Shipping" value={shipping}  onChange={(e)=>setProduct({...product, ['shipping']: e})} className="col-10 col-lg-6 mb-3">
                        <Option value="Yes">Yes</Option>
                        <Option value="No">No</Option>
                    </Select><br/>
                    <Select className="col-10 col-lg-6 mx-auto mb-3" value={selectedCategory} placeholder='Choose Catgeory' onChange={handleCategoryChange}>
                        {categories && categories.map((item)=>{
                            return(
                                <>
                                    <Option key={item._id} value={item._id}>{item.name}</Option>
                                </>
                            )
                        })}
                    </Select><br/>
                    <Select className="col-10 col-lg-6 mx-auto mb-3" mode="multiple" value={arrOfSubsId} placeholder='Choose The Subs' onChange={handleSubCategoryChange}>
                        {subsOption && subsOption.map((item)=>{
                            return(
                                <>
                                    <Option key={item._id} value={item._id} >{item.name}</Option>
                                </>
                            )
                        })}
                    </Select><br/>
                    <Button type="primary" block shape="round" className="col-10 col-lg-6 mx-auto" onClick={handleSubmit}>Submit</Button>
                </div>
            </div>  
        </div>
        </>
    )
}

export default UpdateProduct