import React, {useState, useEffect} from 'react'
import { Button, Input, Modal } from 'antd'
import FileUpload from '../FileUpload'
import { createBlog } from '../../functions/blog'
import {useSelector} from 'react-redux'
import { toast } from 'react-toastify'

const CreateBlogModal = ({isModalVisible, setIsModalVisible, loadAllBlogs})=>{
    const initialState = {
        title:'',
        description:'',
        images:[]
    }
    const [blog, setBlog] = useState(initialState)
    let {title, description} = blog
    let {user} = useSelector((state)=>({...state}))

    const handleChange = (e)=>{
        setBlog({...blog, [e.target.name]:e.target.value})
    }

    const handleSubmit = ()=>{
        createBlog(user.token, blog).then((res)=>{
            setBlog(initialState)
            toast.success(`${title} is successfully created!!`)
            setIsModalVisible(false)
            loadAllBlogs()
        }).catch((err)=>{
            toast.error(`${title} is not created...Try Again!!`)
        })
    }
    return(
        <>
        <Modal title="Create Blog" centered footer={null} visible={isModalVisible} maskClosable={false} className="text-center" onCancel={()=>{setIsModalVisible(false)}}>
            <FileUpload values={blog} setValues={setBlog}/>
            <Input className="col-11 mb-3" name="title" type="text" value={title} onChange={handleChange} placeholder="Blog Title"/><br/>
            <Input className="col-11 mb-3" name="description" type="text" value={description} placeholder="Blog Description" onChange={handleChange}></Input><br/>
            <Button className="col-11 mb-3" type="primary" block shape="round" onClick={handleSubmit}>Submit</Button>
        </Modal>
        </>
    )
}

export default CreateBlogModal