import React, {useState,useEffect}from 'react'
import {Modal, Select} from 'antd'
import { readOrder, updateOrder } from '../../functions/order'
import {useSelector} from 'react-redux'

const OrderInfo = ({infoModal, setInfoModal, modalOrderId, loadAllOrders})=>{
    const [order, setOrder] = useState('')
    const {Option} = Select
    const {user} = useSelector((state)=>({...state}))
    const [options, setOptions] = useState(['Not Proccessed', 'Proccessing', 'Out for Delivery', 'Delivered', 'Cancelled'])
    const [status,setStatus] = useState('')
    useEffect(()=>{
        if(modalOrderId){
            readOrder(user.token, modalOrderId).then((res)=>{
                if(res.data){
                    setOrder(res.data[0])
                    setStatus(res.data[0].deliveryStatus)
                }

            })
        }
    },[modalOrderId])

    const handleStatus = (value)=>{
        setStatus(value)
        updateOrder(user.token, value, order._id).then((res)=>{
            console.log(res.data)
            setInfoModal(false)
            loadAllOrders()
        })
    }
    return(
        <>
        <Modal title='ORDER INFO' visible={infoModal} centered maskClosable={false} onCancel={()=>setInfoModal(false)} footer={false}>
            <div className="container-fluid py-3 mx-auto">
                <div className="prodcut-list">
                    <table className="table table-responsive-md table-bordered mb-3">
                        <thead className="thead-dark">
                            <tr>
                                <th scope="col">Title</th>
                                <th scope="col">Price</th>
                                <th scope="col">Count</th>
                                <th scope="col">Shipping</th>
                            </tr>
                        </thead>
                        <tbody className="table-body-row">
                        {order && order.products.length > 0 && order.products.map((item)=>{
                            let {title, shipping, price, slug} = item.product
                            return(
                                <>
                                    <tr>
                                        <td>{title}</td>
                                        <td>{price}</td>
                                        <td>{item.count}</td>
                                        <td>{shipping}</td>
                                    </tr>
                                </>
                            )
                        })}
                        </tbody>
                    </table>
                </div>
                <div className="order-info">
                    {order ? (<>
                        <p>Order Id: {order.paymentIntent.order_id}</p>
                        <p>Amount: {order.paymentIntent.amount / 100}</p>
                        <p>Email : {order.orderedBy.email}</p>
                        <p>Order Date: {order.createdAt.split('T')[0]}</p>
                        <p>Payment Method: {order.paymentIntent.payment_method}</p>
                        <p>Address: {`House No ${order.orderedBy.address.HouseNo} ${order.orderedBy.address.LandMark} ${order.orderedBy.address.City} ${order.orderedBy.address.State} ${order.orderedBy.address.Pin}`}</p>
                        <p>Phone Number : {order.orderedBy.address.phoneNumber}</p>
                        <Select value={status} onChange={handleStatus} style={{width:'100%'}}>
                            {options && options.map((item)=>{
                                return(
                                    <Option value={item}>{item}</Option>
                                )
                            })}
                        </Select>
                    </>):(<></>)}
                </div>
            </div>
        </Modal>
        </>
    )
}

export default OrderInfo