import React, {useState, useEffect} from 'react'
import Filter from '../../components/Filters'
import Footer from '../../components/home/Footer'
import { listProducts, listProductsCount } from '../../functions/product'
import { createWishlist } from '../../functions/user'
import {useSelector} from 'react-redux'
import './Shop.css'
import {Link} from 'react-router-dom'
import { RatingAvg } from '../../functions/ratings'
import { Pagination, Tooltip } from 'antd'
import { toast } from 'react-toastify'

const Shop = ({history})=>{
    const [products, setProducts] = useState('')
    const [page, setPage] = useState('')
    const [totalProduct, setTotalProduct] = useState(1)
    const {user} = useSelector((state)=>({...state}))
    useEffect(()=>{
        loadAllProduct()
        listProductsCount().then((res)=>setTotalProduct(res.data))
    },[page])

    const loadAllProduct = ()=>{
        listProducts('createdAt', 'desc', page).then((res)=>{setProducts(res.data)})
    }

    const handleAddToWish=(id)=>{
        createWishlist(user.token, id).then((res)=>{
            if(res.data){
                toast.success('Added To Your WishList.....')
            }
        })
    }

    return(
        <>
            <div className="container-fluid pt-3 pt-md-5 pb-5 px-lg-5">
                <div className="row">
                    <div className="col-12 col-lg-3 mx-auto">
                        <Filter setProducts={setProducts} loadAllProduct={loadAllProduct}/>
                    </div>
                    <div className="col-11 col-lg-9 mx-auto products_list_container d-none d-md-block">
                        {/* DESKTOP VIEW */}
                        <div className="row">
                        {products && products.map((item)=>{
                            let {title, images, price,ratings, slug, _id} = item
                            return(
                                <>
                                    <div className="col-12 col-md-6 col-lg-4 my-2 mx-auto related">
                                        <div className="shop_product_container pb-3">
                                            <div className="shop_product_img_container">
                                                <img src={images && images[0].url}/>
                                                <Tooltip title='Add To Wishlist' placement="left">
                                                <i className="fa fa-heart shop_heart" onClick={()=>handleAddToWish(_id)}></i>
                                                </Tooltip>
                                                
                                            </div>
                                            <div className="related_product_body">
                                                <h6 className="pt-2"><Link to={`/product/${slug}`} className="text-dark">{title}</Link></h6>
                                                {RatingAvg(ratings, 'shop')}
                                                <h6 className="pt-2"><i className="fa fa-rupee"></i> {price}</h6>
                                            </div>
                                        </div>
                                    </div>
                                </>
                            )
                        })}
                        </div>
                        <div className="text-center mb-3">
                            <Pagination total={(totalProduct / 9) * 10} current={page} onChange={(value)=>setPage(value)}/>
                        </div>
                    </div>
                </div>
                {/* Mobile View */}
                <div className="d-block d-md-none">
                {products && products.map((item)=>{
                    let {title, images, price,ratings, slug, _id} = item
                    return(
                        <>
                            <div className="mobile_product_card" >
                                <div className="row bg-white">
                                        <div className="col-5" onClick={()=>history.push(`/product/${slug}`)}>
                                            <div className="p_img_cont">
                                                <img src={images && images[0].url} className="img-fluid"/>
                                            </div>
                                        </div>
                                        <div className="col-7 pt-4">
                                            <div className="d-flex justify-content-between info_head align-items-center">
                                                <p>{title}</p>
                                                <Tooltip title='Add To Wishlist' placement="left" color='#ff4545'>
                                                    <i className="fa fa-heart shop_heart" onClick={()=>handleAddToWish(_id)}></i>
                                                </Tooltip>
                                            </div>
                                            
                                            {RatingAvg(ratings, 'shop')}
                                            <p><i className="fa fa-rupee"></i> {price} </p>
                                        </div>
                                </div>
                            </div>
                        </>
                    )
                })}
                <div className="text-center">
                    <Pagination total={(totalProduct / 9) * 10} current={page} onChange={(value)=>setPage(value)}/>
                </div>
                </div>
            </div>
            <Footer/>
        </>
    )
}

export default Shop