import React, {useState} from 'react'
import { Input } from 'antd'
import {LoadingOutlined} from '@ant-design/icons'
import './Login.css'
import { auth } from '../../firebase'
import {toast} from 'react-toastify'
import {useDispatch} from 'react-redux'
import {createOrUpdateUser} from '../../functions/auth'

const RegisterComplete = ({history})=>{
    const [email, setEmail] = useState(localStorage && localStorage.getItem('emailForRegistration'))
    const [password, setPassword] = useState('')
    const [loading, setLoading] = useState(false)
    const dispatch = useDispatch()

    const handleSubmit = async()=>{
        setLoading(true)
       let result =  await auth.signInWithEmailLink(email, window.location.href)
       if(result.user.emailVerified){
           localStorage.removeItem('emailForRegistration')
           const user = auth.currentUser
           await user.updatePassword(password)
           const {token} = await user.getIdTokenResult()
           await createOrUpdateUser(token).then((res)=>{
            let {email, role, _id} = res.data
            dispatch({
              type:'LOGGED_IN_USER',
              payload:{
                _id,
                email,
                role,
                token
              }
            })
            setLoading(false)
          }).catch((err)=>{
            console.log(err)
            setLoading(false)
          })
           toast.success(`You have been successfully logged in as ${result.user.email.split('@')[0]}`)
           setEmail('')
           setPassword('')
           history.push('/')
       }else{
           toast.error('Sorry...We are unable to log you in!!')
           setLoading(false)
       }
    }
    return(
        <>
        <section id="login"> 
            <div className="login-wrapper">
                <div className="container">
                    <div className="row">
                        <div className="col-10 mx-auto ">
                            <img src="Bigberry Logo.png" className="login-logo" alt={'Logo'}/>
                            <h4 className="mt-4 mb-3">Complete Registration</h4>
                            <Input placeholder="Email" type="text" value={email} onChange={(e)=>setEmail(e.target.value)} className="mb-2"/><br/>
                            <Input.Password placeholder="Password" value={password} onChange={(e)=>setPassword(e.target.value)}/>
                            <button className="login-btn" onClick={handleSubmit}>{loading ? <LoadingOutlined/> : 'Login'}</button>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        </>
    )
}

export default RegisterComplete;