import React from 'react'
import axios from 'axios'
import {useSelector} from 'react-redux'
import {useHistory} from 'react-router-dom'
import { removeUserCart } from '../functions/checkOut'

const Payment = ()=>{
    const {user, coupon, COD} = useSelector((state)=>({...state}))
    const history = useHistory()
    const {REACT_APP_API_URL} = process.env

    function loadScript(src) {
        return new Promise((resolve) => {
            const script = document.createElement("script");
            script.src = src;
            script.onload = () => {
                resolve(true);
            };
            script.onerror = () => {
                resolve(false);
            };
            document.body.appendChild(script);
        });
    }

    async function displayRazorpay() {
        const res = await loadScript(
            "https://checkout.razorpay.com/v1/checkout.js"
        );

        if (!res) {
            alert("Razorpay SDK failed to load. Are you online?");
            return;
        }

        // creating a new order
        const result = await axios.post(`${REACT_APP_API_URL}/payment`, {coupon}, {
            headers:{authtoken:user.token}
        });

        if (!result) {
            alert("Server error. Are you online?");
            return;
        }
        console.log('RESULT>>', result)

        // Getting the order details back
        const { amount, id: order_id, currency, receipt } = result.data.order;
        const {address, email} = result.data.user
        const {firstName, phoneNumber} = address

        const options = {
            key: "rzp_test_YGdxAyI0RsABfT", // Enter the Key ID generated from the Dashboard
            amount: amount.toString(),
            currency: currency,
            name: firstName,
            description: "Test Transaction",
            order_id: order_id,
            handler: async function (response) {
                const data = {
                    orderCreationId: order_id,
                    razorpayPaymentId: response.razorpay_payment_id,
                    razorpayOrderId: response.razorpay_order_id,
                    razorpaySignature: response.razorpay_signature,
                    amount:amount,
                    receipt:receipt
                };

                const result = await axios.post(`${REACT_APP_API_URL}/payment/success`, data, {
                    headers:{authtoken:user.token}
                });
                //CALL CREATE ORDER
                alert(result.data.Payment);
                await axios.post(`${REACT_APP_API_URL}/order`, {razorResponse:result.data.Response}, {
                    headers:{authtoken:user.token}
                }).then((res)=>{
                    alert('ORDER PLACED!!!!')
                    removeUserCart(user.token)
                    history.push('/')
                })
            },
            prefill: {
                name: firstName,
                email,
                contact: phoneNumber,
            },
            notes: {
                address: "Soumya Dey Corporate Office",
            },
            theme: {
                color: "#61dafb",
            },
        };

        const paymentObject = new window.Razorpay(options);
        paymentObject.open();
    }
    return(
        <>
            <button className="btn btn-outline-primary" onClick={displayRazorpay}>Pay</button>
        </>
    )
}

export default Payment