import React,{useState, useEffect} from 'react'
import { listBlogs } from '../../functions/blog'
import {useHistory} from 'react-router-dom'
import './HomeBlogs.css'

const HomeBlogs = ()=>{
    const [blogs, setBlogs] = useState([])
    const history = useHistory()
    useEffect(()=>{
        listBlogs(3).then((res)=>{
            setBlogs(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }, [])

    const handleClick = (slug)=>{
        history.push(`/blog/${slug}`)
    }
    return(
        <>
        <section className="blogs py-5 bg-white">
        <div className="container">
                <h5 className="text-green">Our Blogs</h5>
                <hr/>
            </div>
            <div className="container">
                <div className="row">
                    {blogs && blogs.map((item)=>{
                        return(
                            <>
                            <div className="col-11 col-lg-4 mx-auto my-3">
                                <div className="card" data-aos="fade-up" data-aos-duration="6000">
                                    <div className="card-img-top">
                                        <img src={item.images[0].url}/>
                                    </div>
                                    <div className="date">{item.createdAt.split('T')[0]}</div>
                                    <div className="card-body">
                                        <h5>{item.title}</h5>
                                        <p>{item.description.slice(0, 200)}...</p>
                                        <button className="blog-btn" onClick={()=>handleClick(item.slug)}>Read More</button>
                                    </div>
                                </div>
                            </div>
                            </>
                        )
                    })}
                </div>
            </div>
        </section>
        </>
    )
}

export default HomeBlogs