import { Input, Button } from 'antd'
import React, {useState, useEffect} from 'react'
import FileUpload from '../../components/FileUpload'
import AdminNav from '../../components/nav/AdminNav'
import { readBlog, updateBlog } from '../../functions/blog'
import {useSelector} from 'react-redux'
import { toast } from 'react-toastify'

const UpdateBlog = ({match, history})=>{
    const initialState = {
        title:'',
        description:'',
        images:[]
    }
    const [values, setValues] = useState(initialState)
    let {user} = useSelector((state)=>({...state}))
    let {title, description, images} = values
    const {slug} = match.params

    useEffect(()=>{
        readBlog(match.params.slug).then((res)=>{
            setValues(res.data)

        }).catch((err)=>{
            console.log(err)
        })
    }, [])

    const handleChange = (e)=>{
        setValues({...values, [e.target.name]:e.target.value})
    }

    const handleSubmit = ()=>{
        console.log(match.params.slug)
        updateBlog(user.token, slug, values).then((res)=>{
            console.log(res.data)
            history.push(`/admin/blog/all`)
            toast.success(`${title} is successfully updated!!`)
        }).catch((err)=>{
            toast.error(`${title} was not updated!!`)
        })
    }
    return(
        <>
         <div className="container-fluid">
                <div className="row">
                   <AdminNav/>
                    <div className="col-10 mx-auto bg-gray dash-content text-center">
                    <h4 className="text-center pt-5 pb-3">Create Blog</h4>
                        <FileUpload values={values} setValues={setValues}/>
                        <Input className="col-11 col-lg-6 mb-3" name="title" type="text" value={title} onChange={handleChange} placeholder="Blog Title"/><br/>
                        <Input className="col-11 col-lg-6 mb-3" name="description" type="text" value={description} placeholder="Blog Description" onChange={handleChange}></Input><br/>
                        <Button className="col-11 col-lg-6 mb-3" type="primary" block shape="round" onClick={handleSubmit}>Submit</Button>
                    </div>
                </div>  
            </div>
        </>
    )
}

export default UpdateBlog