import React from 'react'
import {Carousel} from 'antd'
import './ImageCarousel.css'

const ImageCarousel = ()=>{
    const carouselContent = ()=>{
        return(
            <>
                <div class="container-fluid pl-5">
					<div class="row height-max align-items-center">
						<div class="col carousel_content">
                            <p className="text-white p-0" data-aos="fade-right" data-aos-duration="1000">WE HELP YOU</p>
                            <h4 className="text-white p-0" data-aos="fade-right" data-aos-duration="1000">TO LIVE A</h4>
                            <h4 className="text-white p-0" data-aos="fade-right" data-aos-duration="1000">HEALTHY LIFE</h4>
                            <button>Know More</button>
						</div>
					</div>
				</div>
            </>
        )
    }
    return(
        <>
        <header className="header">
            <Carousel effect="fade" autoplay className="main_carousel">
                <div  className="carousel_image">
                    {carouselContent()}
                </div>
                <div  className="carousel_image1">
                    {carouselContent()}
                </div>
            </Carousel>
        </header>
        </>
    )
}

export default ImageCarousel;