import React, {useState} from 'react'
import './Cart.css'
import {useDispatch, useSelector} from 'react-redux'
import { InputNumber } from 'antd'
import { userCart } from '../../functions/checkOut'
import {Link} from 'react-router-dom'
import Footer from '../../components/home/Footer'

const Cart = ({history})=>{
    const {cart, user} = useSelector((state)=>({...state}))
    const dispatch = useDispatch()

    const handleCount = (_id, value)=>{
        let cart = JSON.parse(localStorage.getItem('cart'))
        cart.map((c)=>{
            if(c._id == _id){
                c.count = value
            }
            localStorage.setItem('cart', JSON.stringify(cart))
            dispatch({
                type:'ADD_TO_CART',
                payload:cart
            })
        })
    }

    const getTotal = ()=>{
        return cart && cart.reduce((current , next)=>{
            return current + next.count * next.price
        }, 0)
    }

    const handleRemoveFromCart=(_id)=>{
        let cart = []
        if(localStorage.getItem('cart')){
            cart = JSON.parse(localStorage.getItem('cart'))
        }
        cart.map((c, i)=>{
            if(c._id == _id){
                cart.splice(i, 1)
            }
        })
        localStorage.setItem('cart', JSON.stringify(cart))
        dispatch({
            type:'ADD_TO_CART',
            payload:cart
        })
    }

    const handleLoginRedirect = ()=>{
        history.push({
            pathname:'/login',
            state:{from:'/cart'}
        })
    }

    const saveOrderToDb = ()=>{
        userCart(user.token, cart).then((res)=>{
            if(res.data.success){
                history.push('/address')
            }
        }).catch((err)=>{
            console.log(err)
        })
    }
    return(
        <>
        {cart && cart.length > 0 ? (
            <>
             <section>
                <div className="container-fluid py-5 ">
                    <div className="row">
                        <div className="col-11 col-lg-8 mx-auto cart_box">
                        {cart && cart.map((item)=>{
                            let {title, images,_id, count, category, subs, price, quantity} = item
                            return(
                                <>
                                <div className="d-flex justify-content-between flex-wrap mx-auto">
                                    <div className="cart_img_container">
                                        <img src={images && images[0].url}/>
                                    </div>
                                    <div className="pl-4 pt-4">
                                        <h6>{title}</h6>
                                        <p className="text-muted">Category : {category}</p>
                                        <p className="text-muted">Sub : {subs && subs[0].name}</p>
                                        <p className="text-muted">Price : <i className="fa fa-rupee"></i>{price}</p>
                                        <p className="text-muted"><i className="fa fa-trash text-danger pr-2" onClick={()=>handleRemoveFromCart(_id)}> </i>Remove from cart</p>
                                    </div>
                                    <div className="pt-4">
                                        <InputNumber max={quantity} min={1} defaultValue={count} value={count} onChange={(value)=>handleCount(_id, value)}/>
                                    </div>
                                </div>
                    
                                <hr/>
                                </>
                            )
                        })}
                        </div>
                        <div className="col-11 col-lg-3 mx-auto cart_info_box my-4 my-lg-0">
                            <h5 className="text-center pt-3">Order Summary</h5>
                            <hr/>
                            <div className="py-1 px-4">
                                {cart && cart.map((item)=>{
                                    let {title, count, price} = item
                                    return(
                                        <>
                                            <p>{title} X {count}  
                                                <span className="float-right">
                                                    <i className="fa fa-rupee pr-1"></i>
                                                    {price * count}
                                                </span>
                                            </p>
                                        </>
                                    )
                                })}
                            </div>
                            <hr/>
                            <h6 className="px-4">Total Amount  
                                <span className="float-right">
                                    <i className="fa fa-rupee pr-1"></i>
                                    {getTotal()}
                                </span>
                            </h6>
                            <hr/>
                                {user && user.token ? (
                                    <>
                                        <button className="btn btn-outline-primary" onClick={saveOrderToDb}>Proceed To CheckOut</button>
                                    </>
                                ):(
                                    <>
                                        <button className="btn btn-outline-warning" onClick={handleLoginRedirect}>Login To CheckOut</button>
                                    </>
                                )}
                        </div>
                    </div>
                </div>
            </section>
            </>
        ): (
            <>
            <section className="empty-cart my-4">
               <div className="container py-5">
                    <img src="https://media0.giphy.com/media/L40qwPexZfBJLqSBaA/source.gif" className="img-fluid"/>
                    <h6>cart is empty</h6>
                    <p>You don't have any products in the cart yet.<br/>You will find a lot of interesting products on our "Shop" page.</p>
                    <Link className="cart-btn" to="/shop">Return To Shop</Link>
               </div>
            </section>
            </>
        )}
       
        <Footer/>
        </>
    )
}

export default Cart