import axios from 'axios'
const {REACT_APP_API_URL} = process.env

export const listCategories = async()=>{
    return await axios.get(`${REACT_APP_API_URL}/category`)
}

export const readCategory = async(slug)=>{
    return await axios.get(`${REACT_APP_API_URL}/category/${slug}`)
}

export const createCategory = async(authtoken, name)=>{
    return await axios.post(`${REACT_APP_API_URL}/category`, {name}, {
        headers:{authtoken}
    })
}

export const updateCategory = async(authtoken, slug, name)=>{
    return await axios.put(`${REACT_APP_API_URL}/category/update/${slug}`, {name}, {
        headers:{authtoken}
    })
}

export const removeCategory = async(authtoken, slug)=>{
    return await axios.delete(`${REACT_APP_API_URL}/category/${slug}`, {headers: { authtoken}})
}

export const listSubOnCategory = async(id)=>{
    return await axios.get(`${REACT_APP_API_URL}/category/sub/${id}`)
}