import { CheckCircleOutlined, CloseCircleOutlined } from '@ant-design/icons'
import { Button, Input, Radio } from 'antd'
import {toast} from 'react-toastify'
import React, {useState, useEffect} from 'react'
import {useSelector, useDispatch} from 'react-redux'
import { createOrderWithCOD, listUserAddress, listUserCart, removeUserCart } from '../../functions/checkOut'
import { applyCoupon } from '../../functions/coupon'
import './CheckOut.css'
import Payment from '../../components/Payment'

const CheckOut = ({history})=>{
    const [address, setAddress] = useState({})
    const [cart, setCart] = useState([])
    const [total, setTotal] = useState(0)
    const [title, setTitle] = useState('')
    const [discount, setDiscount] = useState(0)
    const [payable, setPayable] = useState(0)
    const dispatch = useDispatch()
    const {user, COD , coupon} = useSelector((state)=>({...state}))
    useEffect(()=>{
        loadUserCart()
        listUserAddress(user && user.token).then((res)=>{
            setAddress(res.data.address)
        })
    }, [user])

    const loadUserCart=()=>{
        listUserCart(user && user.token).then((res)=>{
            setTotal(res.data.totalAmount)
            setCart(res.data.products)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleCoupon=()=>{
        applyCoupon(user.token, {title:title}).then((res)=>{
            if(res.data.discountGiven){
                toast.success(`${title} is successfully applied!!`)
                setDiscount(res.data.discountGiven)
                setPayable(res.data.finalAmount)
                dispatch({
                    type:'COUPON_APPLIED',
                    payload:true
                })
            }
        }).catch((err)=>{
            toast.error(`${title} is not a valid coupon!!`)
        })
    }



    const handleEmptyCart=()=>{
        removeUserCart(user.token).then((res)=>{
            setCart('')
            setTotal(0)
            setDiscount(0)
            setPayable(0)
            localStorage.removeItem('cart')
            dispatch({
                type:'COUPON_APPLIED',
                payload:false
            })
            dispatch({
                type:'ADD_TO_CART',
                payload:[]
            })
            dispatch({
                type:'COD',
                payload:false
            })
            toast.success(res.data.success)
        }).catch((err)=>{
            toast.error(`Your Cart is not emptied...Try Again!!`)
        })
    }

    const handleOrderWithCOD = ()=>{
        createOrderWithCOD(user.token, coupon).then((res)=>{
            toast.success('Your Order Is Successfully Placed!!')
            handleEmptyCart()
            history.push('/')
        }).catch((err)=>{
            toast.error('Your Order Is Not Placed!!')
        })
    }
    return(
        <>
            <div className="container-fluid py-5">
                <div className="row">
                    <div className="col-11 col-lg-8 mx-auto checkout_order_info_cont ">
                        <div className="container">
                            <h5>Billing Details</h5>
                            <h6>{address.firstName} {address.lastName}</h6>
                            <h6>House No {address.HouseNo}</h6>
                            <h6>{address.LandMark}</h6>
                            <h6>{address.City}</h6>
                            <h6>{address.State}</h6>
                            <h6>{address.Pin}</h6>
                            <hr/>
                        </div>
                        <div className="container">
                            {cart && cart.map((item)=>{
                                let {title, images,category, shipping, price} = item.product
                                return(
                                    <>
                                    <div className="d-flex flex-wrap mx-auto">
                                        <div className="cart_img_container">
                                            <img src={images && images[0].url}/>
                                        </div>
                                        <div className="pl-4 pt-4">
                                            <h6>{title}</h6>
                                            <p className="text-muted">Category : {category}</p>
                                            <p className="text-muted">Price : <i className="fa fa-rupee"></i>{price}</p>
                                            <p className="text-muted">Quantity : {item.count}</p>
                                            <p className="text-muted">Shipping :{shipping == 'Yes' ? <CheckCircleOutlined className="text-success pl-2"/> : <CloseCircleOutlined/>}</p>
                                        </div>
                                    </div>
                                    <hr/>
                                    </>
                                )
                            })}
                        </div>
                    </div>
                    <div className="col-11 col-lg-3 mx-auto checkout_order_sum_cont my-3 my-lg-0">
                        <h5 className="text-center pt-3">Order Summary</h5>
                        <hr/>
                        <div className="py-1 px-4">
                            {cart && cart.map((item)=>{
                                let {title} = item.product
                                return(
                                    <>
                                        <p>{title} X {item.count}  
                                            <span className="float-right">
                                                <i className="fa fa-rupee pr-1"></i>
                                                {item.price * item.count}
                                            </span>
                                        </p>
                                    </>
                                )
                            })}
                            {discount > 0 && <p className="text-success pt-1 text-center">Coupon Applied</p>}
                            {discount > 0 && <p>Total <span className="float-right"><i className="fa fa-rupee pr-1"></i>{total}</span></p>}
                            {discount > 0 && <p>Discount <span className="float-right"><i className="fa fa-rupee pr-1"></i>{discount}</span></p>}
                        </div>
                        <hr/>
                        <h6 className="px-4">Total Amount  
                            <span className="float-right">
                                <i className="fa fa-rupee pr-1"></i>
                                {payable > 0 ? payable : total}
                            </span>
                        </h6>
                            
                        <hr/>
                        <h5 className="text-center pt-2">Got Any Coupon ? </h5>
                        <hr/>
                        <Input placeholder="Coupon Name" value={title} onChange={(e)=>setTitle(e.target.value)}/>
                        <Button block className="my-3" type="primary" onClick={handleCoupon} shape="round">Apply</Button>
                        <hr/>
                        <button className="btn btn-outline-warning my-3 mr-3" onClick={handleEmptyCart}>Empty Cart</button>
                        <Payment/>
                        <button className="btn btn-outline-success my-3 ml-3" onClick={handleOrderWithCOD}>COD</button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default CheckOut