import React from 'react';
import {Text, View, StyleSheet } from '@react-pdf/renderer';
import {Table, TableHeader, TableCell, TableBody, DataTableCell} from '@david.kucsai/react-pdf-table'

  const InvoiceTableHeader = ({product, amount, method, status}) => {
      const styles = StyleSheet.create({
          total:{
              marginTop:36
          }
      })
      return(
          <>
          <Table>
            <TableHeader>
                <TableCell>Title</TableCell>
                <TableCell>Price</TableCell>
                <TableCell>Quantity</TableCell>
            </TableHeader>
        </Table>
        <Table>
            <TableBody data={product && product}>
                <DataTableCell getContent={(c)=>c.product.title} />
                <DataTableCell getContent={(c)=>c.product.price} />
                <DataTableCell getContent={(c)=>c.count} />
            </TableBody>
        </Table>
        
        <View>
            <Text style={styles.total}>Total Amount : {amount / 100}</Text>
            <Text>Payment Method : {method} </Text>
            <Text>Payment Status : {status} </Text>
        </View>
          </>
      )
    
  }  
  export default InvoiceTableHeader