import React, {useState, useEffect} from 'react'
import './NewsLetter.css'
import {useSelector} from 'react-redux'
import { updateNewsletter } from '../../functions/user'
import { toast } from 'react-toastify'

const NewsLetter = ()=>{
    const {user} = useSelector((state)=>({...state}))
    const [email, setEmail] = useState('')
    useEffect(()=>{
        setEmail(user && user.email)
    },[user])

    const handleChange = (e)=>{
        setEmail(e.target.value)
    }

    const handleSubmit=()=>{
        if(email.includes('@')){
            updateNewsletter(user && user.token, true).then((res)=>{
                toast.success('Thank You for subscribing our Newsletter!!')
            }).catch((err)=>{
                toast.error('There was some error...Please Try Again!!')
            })
        }else{
            toast.error('Email was not correct...Please Try Again!!')
        }
    }
    return (
        <>
       <section className="newsletter my-5 py-5">
            <div className="container text-center py-5">
                <p className="head">Join Our Newsletter</p>
                <p className="desc">Hey you, sign up it only takes a second to be the first to find out about our latest news and promotions…</p>
                <input type="text" value={email} placeholder="Your Email Address" onChange={handleChange}/><br/>
                <button className="mt-5" onClick={handleSubmit}>Submit</button>
            </div>
       </section>
        </>
    )
}

export default NewsLetter