import axios from 'axios'
const {REACT_APP_API_URL} = process.env

export const listAllBlogs = async()=>{
    return await axios.get(`${REACT_APP_API_URL}/blogs/all`)
}
export const listBlogs = async(sort, order, page)=>{
    return await axios.post(`${REACT_APP_API_URL}/blogs`, {sort, order, page})
}

export const listBlogCount = async()=>{
    return await axios.get(`${REACT_APP_API_URL}/blogs/count`)
}

export const readBlog = async(slug)=>{
    return await axios.get(`${REACT_APP_API_URL}/blog/${slug}`)
}

export const createBlog = async(authtoken, body)=>{
    return await axios.post(`${REACT_APP_API_URL}/blog`, body, {
        headers:{authtoken}
    })
}

export const updateBlog = async(authtoken, slug, body)=>{
    console.log(slug)
    return await axios.put(`http://192.168.43.118:4000/api/blog/update/${slug}`, body, {
        headers:{authtoken}
    })
}

export const removeBlog = async(authtoken, slug)=>{
    return await axios.delete(`http://192.168.43.118:4000/api/blog/${slug}`, {
        headers:{authtoken}
    })
}