import axios from 'axios'
const {REACT_APP_API_URL} = process.env

export const createWishlist = async(authtoken, productId)=>{
    return await axios.post(`${REACT_APP_API_URL}/wishlist`, {productId},{
        headers:{
            authtoken
        }
    })
}

export const listWishlist = async(authtoken)=>{
    return await axios.get(`${REACT_APP_API_URL}/wishlist`,{
        headers:{
            authtoken
        }
    })
}

export const removeWishlist = async(authtoken, id)=>{
    return await axios.put(`${REACT_APP_API_URL}/wishlist/${id}`, {},{
        headers:{
            authtoken
        }
    })
}

export const updateNewsletter = async(authtoken, newsletter)=>{
    return await axios.put(`${REACT_APP_API_URL}/newsletter`, {newsletter}, {
        headers:{
            authtoken
        }
    })
}