import axios from 'axios'
const {REACT_APP_API_URL} = process.env

export const userCart = async(authtoken, cart)=>{
    return await axios.post(`${REACT_APP_API_URL}/user/cart`, {cart},{
        headers:{authtoken}
    })
}

export const listUserCart = async(authtoken)=>{
    return await axios.get(`${REACT_APP_API_URL}/user/cart/list`, {
        headers:{authtoken}
    })
}

export const removeUserCart = async(authtoken)=>{
    return await axios.delete(`${REACT_APP_API_URL}/user/cart/remove`, {
        headers:{authtoken}
    })
}

export const userAddress = async(authtoken, address)=>{
    return await axios.post(`${REACT_APP_API_URL}/user/address`, {address},{
        headers:{authtoken}
    })
}

export const listUserAddress = async(authtoken)=>{
    return await axios.get(`${REACT_APP_API_URL}/user/address/list`, {
        headers:{authtoken}
    })
}

export const createOrderWithCOD = async(authtoken, coupon)=>{
    return await axios.post(`${REACT_APP_API_URL}/order/cod`, {coupon},{
        headers:{authtoken}
    })
}


