import React, {useState, useEffect} from 'react'
import {useSelector} from 'react-redux'
import AdminNav from '../../components/nav/AdminNav'
import { Input, Button } from 'antd'
import { readCategory, updateCategory } from '../../functions/category'
import { toast } from 'react-toastify'

const UpdateCategory = ({match, history})=>{
    const [name, setName] = useState('')
    const [origName, setOrigName] = useState('')
    const {user} = useSelector((state)=>({...state}))

    useEffect(()=>{
        readCategory(match.params.slug).then((res)=>{
            setName(res.data.name)
            setOrigName(res.data.name)
        }).catch((err)=>{
            console.log(err.message)
        })
    }, [])

    const handleSubmit = ()=>{
        updateCategory(user.token, match.params.slug, name).then((res)=>{
            toast.success(`${origName} is updated to ${res.data.name}`)
            history.push('/admin/category')
        }).catch((err)=>{
            toast.error(err.message)
        })
    }
    return(
        <>
        <div className="container-fluid">
            <div className="row">
                <AdminNav/>
                <div className="col-10 mx-auto bg-gray dash-content text-center">
                    <h4 className="text-center pt-5 pb-3">Update Category</h4>
                    <Input type="text" placeholder="Category Name" value={name} onChange={(e)=>setName(e.target.value)} className="col-10 col-lg-6"/><br/>
                    <Button type="primary" shape="round" block className="col-10 col-lg-6 my-3" onClick={handleSubmit}>Submit</Button>
                </div>
            </div>  
        </div>
        </>
    )
}

export default UpdateCategory;