import React, {useState, useEffect} from 'react'
import AdminNav from '../../components/nav/AdminNav'
import {useSelector} from 'react-redux'
import { Button, Input, Select } from 'antd'
import { listCategories } from '../../functions/category'
import { createSub, listSubs, removeSub } from '../../functions/sub'
import {DeleteOutlined, EditOutlined} from '@ant-design/icons'
import {Link} from 'react-router-dom'
import { toast } from 'react-toastify'

const Sub = ()=>{
    const [categories, setCategories] = useState([])
    const [name, setName] = useState('')
    const [category, setCategory] = useState('Choose The Category')
    const [subs, setSubs] = useState('')
    const {user} = useSelector((state)=>({...state}))
    const {Option} = Select

    useEffect(()=>{
        loadAllCategories();
        loadAllSubs();
    }, [])

    const loadAllCategories = ()=>{
        listCategories().then((res)=>{
            setCategories(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const loadAllSubs = ()=>{
        listSubs().then((res)=>{
            setSubs(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleSubmit = ()=>{
        createSub(user.token, {name, category}).then((res)=>{
            setName('')
            loadAllSubs()
            setCategory('Choose The Category')
            toast.success(`${name} is successfully created!!`)
        }).catch((err)=>{
            toast.error(`${name} was not created...Try Again!!`)
        })
    }
    const handleRemove = (slug)=>{
        removeSub(user.token, slug).then((res)=>{
            toast.success(res.data.success)
            loadAllSubs()
        }).catch((err)=>{
            toast.error(err.message)
        })
    }
    return(
        <>
            <div className="container-fluid">
            <div className="row">
                <AdminNav/>
                <div className="col-10 mx-auto bg-gray dash-content text-center">
                    <h4 className="text-center pt-5 pb-3">Create Sub</h4>
                    <Select placeholder="Choose The Category" value={category} className="mb-3 col-10 col-lg-6" onChange={(e)=>setCategory(e)}>
                        {categories && categories.map((item)=>{
                            return(
                                <>
                                    <Option key={item.slug} value={item._id}>{item.name}</Option>
                                </>
                            )
                        })}
                    </Select><br/>
                    <Input type="text" placeholder="Sub Name" value={name} onChange={(e)=>setName(e.target.value)} className="col-10 col-lg-6"/><br/>
                    <Button type="primary" shape="round" block className="col-10 col-lg-6 my-3" onClick={handleSubmit}>Submit</Button>
                    <div className="col-12 col-lg-6 my-3 mx-auto">
                        {subs && subs.map((item)=>{
                            return(
                                <>
                                    <div className="alert alert-primary" key={item._id}>{item.name}
                                        <span className="float-right text-danger">{<DeleteOutlined onClick={()=>handleRemove(item.slug)}/>}</span>
                                        <span className="float-right pr-2">{<Link to={`/admin/update/sub/${item.slug}`}><EditOutlined className=" text-warning"/></Link>}</span>
                                    </div>
                                </>
                            )
                        })}
                    </div>
                </div>
            </div>  
        </div>
        </>
    )
}

export default Sub