import React,{useState, useEffect} from 'react'
import { Button, Input } from 'antd'
import AdminNav from '../../components/nav/AdminNav'
import {Link} from 'react-router-dom'
import { createCategory, listCategories, removeCategory } from '../../functions/category'
import {useSelector} from 'react-redux'
import { toast } from 'react-toastify'
import { DeleteOutlined, EditOutlined } from '@ant-design/icons'
import './Category.css'

const Category = ()=>{
    const [category, setCategory] = useState([])
    const [name, setName] = useState('')
    const {user} = useSelector((state)=>({...state}))
    useEffect(()=>{
        loadAllCategories()
    }, [])

    const loadAllCategories = ()=>{
        listCategories().then((res)=>{
            setCategory(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleSubmit = ()=>{
        createCategory(user.token, name).then((res)=>{
            toast.success(`${name} has been successfully created!!`)
            loadAllCategories()
            setName('')
        }).catch((err)=>{
            toast.error(`${name} was not created...Try Again!!`)
            setName('')
        })
    }

    const handleRemove = (slug)=>{
        removeCategory(user.token, slug).then((res)=>{
            toast.success(`${slug} is successfully deleted!!`)
            loadAllCategories()
        }).catch((err)=>{
            toast.error(err.message)
        })
    }
    return(
        <>
        <div className="container-fluid">
            <div className="row">
                <AdminNav/>
                <div className="col-10 mx-auto bg-gray dash-content text-center">
                    <h4 className="text-center pt-5 pb-3">Create Category</h4>
                    <Input type="text" placeholder="Category Name" value={name} onChange={(e)=>setName(e.target.value)} className="col-10 col-lg-6"/><br/>
                    <Button type="primary" shape="round" block className="col-10 col-lg-6 my-3" onClick={handleSubmit}>Submit</Button>
                    <div className="col-12 col-lg-6 my-3 mx-auto">
                        {category && category.map((item)=>{
                            return(
                                <>
                                    <div className="alert alert-primary list_box" key={item._id}>{item.name}
                                        <span className="float-right text-danger">{<DeleteOutlined onClick={()=>handleRemove(item.slug)}/>}</span>
                                        <span className="float-right pr-2">{<Link to={`/admin/update/category/${item.slug}`}><EditOutlined className=" text-warning"/></Link>}</span>
                                    </div>
                                </>
                            )
                        })}
                    </div>
                </div>
            </div>  
        </div>
        </>
    )
}

export default Category