import axios from 'axios'
const {REACT_APP_API_URL} = process.env

export const listCoupons = async(authtoken)=>{
    return await axios.get(`${REACT_APP_API_URL}/coupons`, {headers:{authtoken}})
}

export const readCoupon = async(authtoken, slug)=>{
    return await axios.get(`${REACT_APP_API_URL}/coupon/${slug}`, {headers:{authtoken}})
}

export const createCoupon = async(authtoken, coupon)=>{
    return await axios.post(`${REACT_APP_API_URL}/coupon`, {coupon}, {
        headers:{
            authtoken
        }
    })
}

export const updateCoupon = async(authtoken, coupon, slug)=>{
    return await axios.put(`${REACT_APP_API_URL}/coupon/${slug}`, {coupon}, {
        headers:{authtoken}
    })
}

export const removeCoupon = async(authtoken, slug)=>{
    return await axios.delete(`${REACT_APP_API_URL}/coupon/${slug}`, {
        headers:{authtoken}
    })
}

export const applyCoupon = async(authtoken, title)=>{
    return await axios.post(`${REACT_APP_API_URL}/coupon/apply`, title, {
        headers:{authtoken}
    })
}