import React,{useState, useEffect} from 'react'
import AdminNav from '../../components/nav/AdminNav'
import {Input, Button} from 'antd'
import { readCoupon, updateCoupon } from '../../functions/coupon'
import {useSelector} from 'react-redux'
import { toast } from 'react-toastify'

const UpdateCoupon = ({match, history})=>{
    const [title, setTitle] = useState('') 
    const [discountRate, setDiscountRate] = useState(0)
    const [expiry, setExpiry] = useState('')
    const {user} = useSelector(state => ({...state}))

    useEffect(()=>{
        readCoupon(user.token, match.params.slug).then((res)=>{
            setTitle(res.data.title)
            setDiscountRate(res.data.discountRate)
            setExpiry(res.data.expiry.split('T')[0])
        })
    },[match.params.slug])

    const handleSubmit=()=>{
        updateCoupon(user.token, {title, expiry, discountRate}, match.params.slug).then((res)=>{
            history.push('/admin/coupon')
            toast.success(`${title} is successfully updated!!`)
        }).catch((err)=>{
            toast.error(`${title} is not updated....Try Again!!`)
        })
    }
    return(
        <>
        <div className="container-fluid">
                <div className="row">
                   <AdminNav/>
                    <div className="col-10 mx-auto bg-gray dash-content text-center">
                        <h4 className="text-center pt-5 pb-3">Update Coupon</h4>
                        <Input type="text" placeholder="Coupon Title" value={title} onChange={(e)=>setTitle(e.target.value)} className="col-10 col-lg-6 mb-3"/><br/>
                        <Input type="number" placeholder="Discount Rate" value={discountRate} onChange={(e)=>setDiscountRate(e.target.value)} className="col-10 col-lg-6 mb-3"/><br/>
                        <Input type="date" placeholder="Expiry Date" value={expiry} onChange={(e)=>setExpiry(e.target.value)} className="col-10 col-lg-6"/><br/>
                        <Button type="primary" shape="round" block className="col-10 col-lg-6 my-3" onClick={handleSubmit}>Submit</Button>
                    </div>
                </div>  
            </div>
        </>
    )
}

export default UpdateCoupon