import React, {useState} from 'react'
import './Login.css'
import { Input } from 'antd'
import {useDispatch, useSelector} from 'react-redux'
import { auth, googleAuthProvider } from '../../firebase'
import { toast } from 'react-toastify'
import { GoogleOutlined, LoadingOutlined } from '@ant-design/icons'
import {createOrUpdateUser} from '../../functions/auth'
import ForgetPassword from '../../components/ForgetPassword'
import RegisterForm from '../../components/RegisterForm'

const Login = ({history})=>{
    const [email, setEmail] = useState('mishraabhinav710@gmail.com')
    const [password, setPassword] = useState('anshu11')
    const [loading, setLoading] = useState(false)
    const [passwordModal, setPasswordModal] = useState(false)
    const [isModalVisible, setIsModalVisible] = useState(false)
    const dispatch = useDispatch()

    const roleBasedRedirect = (data)=>{
      let intended = history.location.state
      if(intended){
        history.push(intended.from)
      }else{
        if(data.role === 'admin'){
          history.push('/admin/dashboard')
        }else if(data.role === 'subscriber'){
          history.push('/')
        }
      }
    }

    const handleLogin = async()=>{
      setLoading(true)
      await auth.signInWithEmailAndPassword(email, password).then(async(res)=>{
        if(res.user.emailVerified){
          let {token} = await res.user.getIdTokenResult()
          await createOrUpdateUser(token).then((res)=>{
            let {email, role, _id} = res.data
            dispatch({
              type:'LOGGED_IN_USER',
              payload:{
                _id,
                email,
                role,
                token
              }
            })
            setLoading(false)
            toast.success(`You have successfully logged in as ${email.split('@')[0]}`)
            roleBasedRedirect(res.data)
          }).catch((err)=>{
            console.log(err)
          })
        }
      }).catch((err)=>{
          setLoading(false)
        toast.error('Login Failed...Try Again!!')
      })
    }

    const handleGoogleLogin = async()=>{
      await auth.signInWithPopup(googleAuthProvider).then(async(res)=>{
        if(res.user.emailVerified){
          let {token} = await res.user.getIdTokenResult()
          await createOrUpdateUser(token).then((res)=>{
            let {email, role, _id, newsletter} = res.data
            dispatch({
              type:'LOGGED_IN_USER',
              payload:{
                _id,
                email,
                role,
                newsletter,
                token
              }
            })
          }).catch((err)=>{
            console.log(err)
          })
          toast.success(`You have successfully logged in as ${email.split('@')[0]}`)
        }
      }).catch((err)=>{
        toast.error('Sorry...Try Once Again!!')
      })
    }

    const handleCheck = () => {
      const x = document.querySelector('.pass')
      if(x.type === 'password'){
        x.type = 'text'
      }else{
        x.type = 'password'
      }
    }
    return(
        <section className="sign-in my-5">
        <div className="container">
            <div className="row justify-content-center">
                <div className="col-12 col-lg-5 text-center">
                    <div className="user-form-card  py-5">
                        <h2 className="wlcm-text">Welcome</h2>
                        <p className="sub-heads ">Use Your Credentials To Access</p>
                        <div className="user-form my-4">
                            <input type="text" placeholder="Enter Your Email" value={email} onChange={(e)=>setEmail(e.target.value)} className="form-control mb-3"/>
                            <input type="password" placeholder="Enter Your Password" value={password} onChange={(e)=>setPassword(e.target.value)} className="form-control pass mb-3"/>
                            <input type="checkbox" className="form-check-input float-left" onClick={handleCheck}/>Show Password
                        </div>
                        <button className="user-login-button" onClick={handleLogin}>{loading ?  <LoadingOutlined/> : 'Login' }</button>
                        <button className="user-login-button bg-danger" onClick={handleGoogleLogin}>Google Login</button>
                        <p className="pt-3">Forgot Your Password ? <span className="text-green font-weight-bold reset" onClick={()=>setPasswordModal(true)}>Reset Here</span></p>
                        <ForgetPassword setPasswordModal={setPasswordModal} passwordModal={passwordModal}/>
                    </div>
                    <div className="sign-up-cont pt-4 pb-2">
                        <p>Don't Have Any Account ? <span className="text-green font-weight-bold" onClick={()=>setIsModalVisible(true)}>Sign Up Here</span></p>
                        <RegisterForm isModalVisible={isModalVisible} setIsModalVisible={setIsModalVisible}/>
                    </div>
                </div>
            </div>
        </div>
    </section>
    )
}

export default Login