import React, {useState, useEffect} from 'react'
import ProductCarousel from '../../components/ProductCarousel'
import { listRelatedProducts, rateProduct, readProduct } from '../../functions/product'
import {Link} from 'react-router-dom'
import './ProductInfo.css'
import {useDispatch, useSelector} from 'react-redux'
import Footer from '../../components/home/Footer'
import _ from 'lodash'
import RateModal from '../../components/modals/RateModal'
import { toast } from 'react-toastify'
import { RatingAvg } from '../../functions/ratings'
import ProductInfoTab from '../../components/ProductInfoTab'
import { createWishlist } from '../../functions/user'

const ProductInfo = ({match, history})=>{
    const [product, setProduct] = useState('')
    const [related, setRelated] = useState({})
    const [visible, setVisible] = useState(false)
    const [comment, setComment] = useState('')
    const [rating, setRating] = useState(0)
    const dispatch = useDispatch()
    const {user} = useSelector(state => ({...state}))
    let {images, title,price, description, category, shipping, quantity} = product
    useEffect(()=>{
        loadProductInfo()
    }, [match.params.slug])
    useEffect(()=>{
        loadRelatedProduct()
    }, [product._id])

    const loadProductInfo = ()=>{
        readProduct(match.params.slug).then((res)=>{
            let {ratings} = res.data
            setProduct(res.data)
            if(user && user.token){
                if(ratings.length > 0){
                    ratings.map((item)=>{
                        if(item.postedBy._id == user._id){
                            console.log(item)
                            setRating(item.star)
                            setComment(item.comment)
                        }
                    })
                }
            }
        }).catch((err)=>{
            console.log(err)
        })
    }

    const loadRelatedProduct = ()=>{
        listRelatedProducts(product._id).then((res)=>{
            setRelated(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleAddToCart = ()=>{
        let cart = []
        if(localStorage.getItem('cart')){
            cart = JSON.parse(localStorage.getItem('cart'))
        }
        cart.push({...product, count:1})
        let unique = _.uniqWith(cart, _.isEqual)
        localStorage.setItem('cart', JSON.stringify(unique))
        dispatch({
            type:'ADD_TO_CART',
            payload:unique
        })
        dispatch({
            type:'SET_VISIBILITY',
            payload:true
        })
    }

    const handleRateModal = ()=>{
        if(user && user.token){
            setVisible(true)
        }else{
            history.push({
                pathname:'/login',
                state:{from:`/product/${match.params.slug}`}
            })
        }
    }

    const handleRating = (value)=>{
        setRating(value)
    }

    const submitRating = ()=>{
        rateProduct(user && user.token, product._id, rating, comment).then((res)=>{
            if(res.data){
                setVisible(false)
                loadProductInfo()
                toast.success(`Thank You For You Rating!!`)
            }
        }).catch((err)=>{
            toast.error('Your Rating is not posted..Try Again!!')
        })
    }

    const handleAddToWish=(id)=>{
        createWishlist(user.token, id).then((res)=>{
            if(res.data){
                toast.success('Added To Your WishList.....')
            }
        })
    }
    return(
        <>
        <section className="bg-white pb-5 mt-4">
            <div className="container-fluid py-5">
                <div className="row">
                    <div className="col-12 col-md-11 col-lg-5 mx-auto">
                        <ProductCarousel images={images}/>
                    </div>
                    <div className="col-11 col-lg-6 mx-auto my-3">
                        {RatingAvg(product.ratings)}
                        <h3 className="font-weight-bold mb-3 product_title pb-3">{title}</h3>
                        <p className="text-muted pb-3">{description && description.slice(0, 246)}</p>
                        <p className="product-sub">Category : <span>{category}</span></p>
                        <p className="product-sub">Availability : <span className={quantity > 0 ? 'text-success' : 'text-danger'}>{quantity > 0 ? 'In Stock' : 'Out Of Stock'}</span></p>
                        <p className="product-sub">Shipping : <span>{shipping}</span></p>
                        <h4 className="mb-4 pt-3"><i className="fa fa-rupee"></i> {price}</h4>
                        <button className="add-to-cart-btn" disabled={quantity == 0} onClick={handleAddToCart}>Add To Cart <i className="fa fa-shopping-cart"></i></button>
                        <button className="add-to-wishlist-btn ml-2" onClick={()=>handleAddToWish(product._id)}>Add To wishlist <i className="fa fa-heart"></i></button>
                        <div className="mt-4">
                        <ProductInfoTab description={description} review={product.ratings}>
                            <button className="msg-btn mt-4" onClick={handleRateModal}>{user && user.token ? 'Leave A Rating' : 'Login To Review'}</button>
                            <RateModal visible={visible} submitRating={submitRating} setVisible={setVisible} comment={comment} setComment={setComment} rating={rating} handleRating={handleRating}/>
                        </ProductInfoTab>
                        </div>
                    </div>
                </div>
                {/* <hr/> */}
                <hr/>
                    <h3 className="text-center pb-3">Related Products</h3>
                <hr/>
                <div className="container-fluid mt-5">
                    <div className="row">
                    {related.length > 0 && related.map((item)=>{
                        let {images,title, price,ratings, slug} = item
                        return(
                            <>
                            <div className="col-12 col-md-6 col-lg-4 my-2 mx-auto related">
                                <div className="realted_product_container">
                                    <div className="related_product_img_container">
                                        <img src={images && images[0].url}/>
                                    </div>
                                    <div className="related_product_body">
                                    <h5 className="pt-3"><Link to={`/product/${slug}`}>{title}</Link></h5>
                                        {RatingAvg(ratings)}
                                        <h6 className="pt-2"><i className="fa fa-rupee"></i> {price}</h6>
                                        
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 col-md-6 col-lg-4 my-2 mx-auto related">
                                <div className="realted_product_container">
                                    <div className="related_product_img_container">
                                        <img src={images && images[0].url}/>
                                    </div>
                                    <div className="related_product_body">
                                    <h5 className="pt-3"><Link to={`/product/${slug}`}>{title}</Link></h5>
                                        {RatingAvg(ratings)}
                                        <h6 className="pt-2"><i className="fa fa-rupee"></i> {price}</h6>
                                        
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 col-md-6 col-lg-4 mt-3 mx-auto related">
                                <div className="realted_product_container">
                                    <div className="related_product_img_container">
                                        <img src={images && images[0].url}/>
                                    </div>
                                    <div className="related_product_body">
                                    <h5 className="pt-3"><Link to={`/product/${slug}`}>{title}</Link></h5>
                                        {RatingAvg(ratings)}
                                        <h6 className="pt-2"><i className="fa fa-rupee"></i> {price}</h6>
                                        
                                    </div>
                                </div>
                            </div>
                            
                            </>
                        )
                    })}
                    </div>
                </div>
                
            </div>
        </section>
        <div className="my-4"></div>
        <Footer/>
        </>
    )
}

export default ProductInfo