import React, {useState, useEffect} from 'react'
import About from '../../components/home/About'
import Footer from '../../components/home/Footer'
import HomeBlogs from '../../components/home/HomeBlogs'
import HomeProduct from '../../components/home/HomeProduct'
import ImageCarousel from '../../components/home/ImageCarousel'
import NewsLetter from '../../components/home/Newsletter'
import Services from '../../components/home/Services'
import Testimonial from '../../components/home/Testimonial'
import {useSelector} from 'react-redux'
import {Modal} from 'antd'
import './Home.css'

const Home = ()=>{
    const [newsletter, setNewsletter] = useState(false)
    const {user} = useSelector((state)=>({...state}))
    const [isModalVisible, setModalVisible] = useState(false)
    console.log(process.env.REACT_APP_API_URL)

    useEffect(()=>{
        setNewsletter(user && user.newsletter)
    },[user])

    useEffect(()=>{
        setTimeout(()=>{
            setModalVisible(true)
        }, 2000)
    }, [])
    return(
        <>
            <ImageCarousel/>
            <HomeProduct criteria={'createdAt'} title={'New Arrivals'}/>
            <HomeProduct criteria={'sold'} title={'Best Selling Products'}/>
            <About/>
            {newsletter ? (
                <>
                </>
            ):(
                <>
                    <NewsLetter/>
                </>
            )}
            <Services/>
            <HomeBlogs/>
            <Testimonial/>
            <Footer/>
            <Modal className="pre_release_modal"  centered visible={isModalVisible} footer={null} onCancel={()=>setModalVisible(false)}>
                <img src="https://i.pinimg.com/originals/01/32/31/01323190cd6933de96287a5804fd636a.gif" className="img-fluid"/>
                <div className="modal_content">
                    <p className="text-center head">BIGBERRY ECOMMERCE BETA</p>
                    <p className="desc">This is the pre-release of <span className="main">BIGBERRY PHARMA ECOMMMERCE BUSINESS</span>. The website will be launched officially very soon.</p>
                </div>
                
            </Modal>
        </>
    )
}

export default Home