import React, {useState , useEffect} from 'react'
import AdminNav from '../nav/AdminNav'
import { listCategories, listSubOnCategory } from '../../functions/category'
import {useSelector} from 'react-redux'
import { Button, Input, Select, Modal } from 'antd'
import { createProduct, listProducts } from '../../functions/product'
import {toast} from 'react-toastify'
import FileUpload from '../FileUpload'

const CreateProductModal = ({isModalVisible, setIsModalVisible, loadAllProducts})=>{
    const initialState = {
        title:'',
        description:'',
        price:'',
        category:'',
        subs:[],
        quantity:'',
        shipping:'',
        images:[]
    }
    const [product, setProduct] = useState(initialState)
    const [categories, setCategories] = useState([])
    const [relatedSub, setRelatedSub] = useState([])
    const {user} = useSelector((state)=>({...state}))
    let {title, description, price, category, subs, quantity, shipping} = product
    const {Option} = Select

    useEffect(()=>{
        loadAllCategories()
    }, [])

    const loadAllCategories = ()=>{
        listCategories().then((res)=>{
            setCategories(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleChange=(e)=>{
        setProduct({...product, [e.target.name]: e.target.value})
    }
    
    const handleCategoryChange = (value)=>{
        setProduct({...product, ['category']: value, ['subs']:[]})
        listSubOnCategory(value).then((res)=>{
            setRelatedSub(res.data)
            
        }).catch((err)=>{
            console.log(err)
        })
    }


    const handleSubmit = ()=>{
       createProduct(user.token, product).then((res)=>{
           setProduct(initialState)
           toast.success(`${res.data.title} is successfully created!!`)
           setIsModalVisible(false)
           loadAllProducts()
       }).catch((err)=>{
            toast.error(`${title} is not created...Try Again!!`)
       })
    }
    return(
        <>
            <Modal title="Create Product" centered footer={null} visible={isModalVisible} maskClosable={false} className="text-center" onCancel={()=>{setIsModalVisible(false); setProduct(initialState)}}>
                    <FileUpload values={product} setValues={setProduct}/>
                    <Input type="text" placeholder="Product Name" value={title} onChange={handleChange} name='title' className="col-11 mb-3"/><br/>
                    <Input type="text" placeholder="Product description" value={description} onChange={handleChange} name='description' className="col-11  mb-3"/><br/>
                    <Input type="number" placeholder="Product Price" value={price} onChange={handleChange} name='price' className="col-11  mb-3"/><br/>
                    <Input type="number" placeholder="Product Quantity" value={quantity} onChange={handleChange} name='quantity' className="col-11  mb-3"/><br/>
                    <Select  placeholder="Shipping"  onChange={(e)=>setProduct({...product, ['shipping']: e})} className="col-11  mb-3">
                        <Option value="Yes">Yes</Option>
                        <Option value="No">No</Option>
                    </Select><br/>
                    <Select className="col-11  mx-auto mb-3" placeholder='Choose Catgeory' onChange={handleCategoryChange}>
                        {categories && categories.map((item)=>{
                            return(
                                <>
                                    <Option key={item._id} value={item._id}>{item.name}</Option>
                                </>
                            )
                        })}
                    </Select><br/>
                    <Select className="col-11 mx-auto mb-3" mode="multiple" value={subs} placeholder='Choose Sub' onChange={(e)=>{setProduct({...product, ['subs']:e})}}>
                        {relatedSub && relatedSub.map((item)=>{
                            return(
                                <>
                                    <Option key={item._id} value={item._id}>{item.name}</Option>
                                </>
                            )
                        })}
                    </Select><br/>
                    <Button type="primary" block shape="round" className="col-11 mx-auto" onClick={handleSubmit}>Submit</Button>
            </Modal>
                    
        </>
    )
}

export default CreateProductModal