import React, {useState, useEffect} from 'react'
import { readOrder } from '../../functions/order'
import {useSelector} from 'react-redux'
import './UserOrderInfo.css'
import Footer from '../../components/home/Footer'
import {Progress, Tooltip} from 'antd'
import Invoice from '../../components/pdf/Invoice'
import { PDFDownloadLink, PDFViewer } from '@react-pdf/renderer'

const UserOrderInfo = ({match})=>{
    const [orderInfo, setOrderInfo] = useState('')
    const [percent, setPercent] = useState(0)
    const [status, setStatus] = useState('')
    const [titleTip, setTitleTip] = useState('Not Proccessed')
    const [loading, setLoading] = useState(true)
    const {user} = useSelector((state)=>({...state}))
    let {id} = match.params
    useEffect(()=>{
        readOrder(user && user.token, id).then((res)=>{
            setOrderInfo(res.data)
            if(res.data){
                let {deliveryStatus} = res.data[0]
                if(deliveryStatus == 'Not Processed'){
                    setTitleTip(deliveryStatus)
                    setPercent(0)
                }else if(deliveryStatus == 'Cancelled'){
                    setPercent(100)
                    setTitleTip(deliveryStatus)
                    setStatus("exception")
                }else if(deliveryStatus == 'Proccessing'){
                    setPercent(33.3)
                    setTitleTip(deliveryStatus)
                }else if(deliveryStatus == 'Out for Delivery'){
                    setPercent(66.6)
                    setTitleTip(deliveryStatus)
                }else if(deliveryStatus == 'Delivered'){
                    setPercent(100)
                    setTitleTip(deliveryStatus)
                }
            }
            setLoading(false)
        }).catch((err)=>{
            console.log(err)
        })
    },[id, user])
    return(
        <>
            <div className="container my-5">
                <div className="row">
                    <div className="order_info_address_box col-12 mx-auto mb-4">
                        <div className="row">
                            {orderInfo && orderInfo.map((item)=>{
                                let {City, LandMark, HouseNo, Pin, State, firstName, lastName, phoneNumber} = item.orderedBy.address
                                return (
                                    <>
                                    <div className="col-12 col-lg-6 py-3">
                                        <h6>Delivery Address</h6>
                                        <p className="font-text-bold">{firstName} {lastName}</p>
                                        <p>{HouseNo} {LandMark} {City} - {Pin}, {State}</p>
                                        <p>Phone Number</p>
                                        <p>{phoneNumber}</p>
                                    </div>
                                    <div className="col-12 col-lg-6 py-3">
                                        <h6>More Options</h6>
                                        <div className="d-flex justify-content-between">
                                            <p><i className="fa fa-file pr-3 text-primary"></i> Download Invoice</p>
                                            {(loading == false) ? (
                                                <>
                                                <PDFDownloadLink fileName="invoice" document={<Invoice data={orderInfo && orderInfo}/>}>
                                                    <button className="btn btn-sm btn-outline-primary">Download Invoice</button>
                                                </PDFDownloadLink>

                                                </>
                                                ) : (
                                                <>
                                                    <p>Downlaod Later!!</p>
                                                </>)
                                            }
                                            
                                        </div>         
                                    </div>
                                    </>
                                )
                            })}
                        </div>
                    </div>
                    <div className="order_info_address_box col-12 mx-auto">
                        <div className="row align-items-center">
                            {orderInfo && orderInfo.map((item)=>{
                                let {images, title} = item.products[0].product
                                return(
                                    <>
                                        <div className="col-2 col-lg-3 order_img_container">
                                            <img src={images && images[0].url}/>
                                        </div>
                                        <div className="col-7 col-lg-3 ml-auto">
                                            <p>{title}</p>
                                            <p><i className="fa fa-rupee"></i> {item.paymentIntent.amount / 100}</p>
                                            <Tooltip title={titleTip} className="d-block d-lg-none">
                                                <Progress percent={percent} status={status}/>
                                            </Tooltip>
                                        </div>
                                        <div className="col-5 col-lg-3 d-none d-lg-block">
                                            <Tooltip title={titleTip}>
                                                <Progress percent={percent} status={status}/>
                                            </Tooltip>
                                            
                                        </div>
                                    </>
                                )
                            })}
                          
                           <div className="col-lg-3">

                           </div>
                            
                           
                           
                        </div>
                    </div>
                </div>
            </div>
            <Footer/>

        </>
    )
}

export default UserOrderInfo