import React, {useState, useEffect} from 'react'
import Footer from '../../components/home/Footer'
import { listBlogs, listBlogCount } from '../../functions/blog'
import './BlogPage.css'
import {Link} from 'react-router-dom'
import {Pagination} from 'antd'

const BlogPage = ()=>{
    const [blog, setBlog] = useState('')
    const [recentBlog, setRecentBlog] = useState('')
    const [totalBlog, setTotalBlog] = useState(0)
    const [page, setPage] = useState(1)

    useEffect(()=>{
        loadAllBlogs()
    }, [page])
    
    useEffect(()=>{
        loadAllRecent()
        getTotalCount()
    },[])

    const loadAllBlogs = ()=>{
        listBlogs('createdAt', 'asc', page).then((res)=>{
            setBlog(res.data)
        })
    }
    const loadAllRecent = ()=>{
        listBlogs('createdAt', 'desc', 1).then((res)=>{
            setRecentBlog(res.data)
        })
    }

    const getTotalCount = ()=>{
        listBlogCount().then((res)=>{
            setTotalBlog(res.data)
        })
    }
    return(
        <>
        <section className="py-5">
            <div className="container">
                <div className="row">
                    <div className="col-12 col-lg-8 mx-auto">
                    {blog && blog.map((item)=>{
                        const {images, title, description, slug, createdAt} = item
                        return(
                            <>
                                <div className="col-12 mb-5 blog_content" data-aos="fade-up" data-aos-duration="1000">
                                    <div className="card">
                                        <div className="card-img-top">
                                            <img src={images[0].url}/>
                                        </div>
                                        <div className="card-body">
                                            <h4>{title}</h4>
                                            <p>{new Date(createdAt.split('T')[0]).toDateString()}</p>
                                            <p>{description.slice(0, 240)} [...]</p>
                                            <Link to={`/blog/${slug}`}>Read More</Link>
                                        </div>
                                    </div>
                                </div>
                            </>
                        )
                    })}
                    <Pagination className="text-center" total={(totalBlog / 3) * 10} current={page} onChange={(e)=>setPage(e)}/>
                    </div>
                    {/* Recent */}
                    <div className="col-lg-4 d-none d-lg-block">
                        <div className="card">
                            <div className="card-body text-center">
                                <h6>Recent Posts</h6>
                                <hr/>
                                {recentBlog && recentBlog.map((item)=>{
                                    return(
                                        <>
                                        <div className="new-blog mb-5">
                                            <img src={item.images[0].url}/>
                                            <p>{item.title}</p>
                                        </div>
                                        </>
                                    )
                                })}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <Footer/>
        </>
    )
}

export default BlogPage