import React,{useState, useEffect} from 'react'
import './BlogInfo.css'
import { readBlog } from '../../functions/blog'
import Footer from '../../components/home/Footer'


const BlogInfo = ({match})=>{
    let [info, setInfo] = useState('')
    let {slug} = match.params
    useEffect(()=>{
       readBlog(slug).then((res)=>{
           console.log(res.data)
           setInfo(res.data)
       }).catch((err)=>{
           console.log(err)
       })
    },[slug])
    return(
        <>
        <section className="blog_info my-4 py-3">
            <div className="blog_info_img_container text-center w-100">
                <img src={info && info.images[0].url} className=""/>
            </div>
            <div className="container py-3">
                    <div className="row">
                        <div className="col-12 mx-auto">
                            <div className="blog-content mt-3">
                                <h4>{info && info.title}</h4>
                                <p className="text-muted">{info && info.description}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Footer/>
        </>
    )
}

export default BlogInfo