import React, {useState} from 'react'
import {auth} from '../firebase'
import {Button, Input, Modal} from 'antd'
import {toast} from 'react-toastify'

const ForgetPassword = ({passwordModal, setPasswordModal})=>{
    const [email, setEmail] = useState('')

    const handleSubmit = async()=>{
        const config = {
            url:`${process.env.REACT_APP_FORGOT_PASS_REDIRECT_URL}/login`,
            handleCodeInApp:true
        }
       await auth.sendPasswordResetEmail(email, config).then((res)=>{
           console.log('FORGOT PASS>>', res)
           toast.success(`Confirmation Link has been sent to ${email}`)
           setPasswordModal(false)
       }).catch((err)=>{
           toast.error('Sorry...we were unable to send the confirmation link!!')
       })
    }
    
    return(
        <>
        <Modal title="Forgot Password ?" footer={null} visible={passwordModal} onCancel={()=>setPasswordModal(false)}>
            <Input type="text" value={email} onChange={(e)=>setEmail(e.target.value)} className="form-control" placeholder="Enter Your Email"/>
            <Button disabled={!email.includes('@')} type="primary" shape="round" block className="mt-3" onClick={handleSubmit}>Submit</Button>
        </Modal>
        </>
    )
}

export default ForgetPassword