import React, {useState, useEffect} from 'react'
import {useSelector} from 'react-redux'
import { toast } from 'react-toastify'
import Footer from '../../components/home/Footer'
import { listUserAddress, userAddress } from '../../functions/checkOut'
import './Profile.css'

const Test = ()=>{
    const initialState = {
        firstName:'',
        lastName:'',
        phoneNumber:'',
        HouseNo:'',
        LandMark:'',
        City:'',
        Pin:'',
        State:''
    }
    const {user} = useSelector((state)=>({...state}))
    const [address, setAddress] = useState(initialState)
    const [edit, setEdit] = useState(false)
    useEffect(()=>{
        listUserAddress(user && user.token).then((res)=>{
            setAddress(res.data.address)
        }).catch((err)=>{
            console.log(err.message)
        })
    },[user])

    let {firstName, lastName, phoneNumber, HouseNo, LandMark,City, State, Pin} = address

    const handleChange = (e)=>{
        setEdit(true)
        setAddress({...address, [e.target.name] : e.target.value})
    }
    const handleClick=()=>{
        userAddress(user && user.token, address).then((res)=>{
            setEdit(false)
            if(res.data.success){
                toast.success('Profile is successfully Updated!!')
            }else{
                toast.error('Profile is not updated...Try Again!!')
            }
        }).catch((err)=>{
            console.log(err)
        })
    }
    return(
        <>
       <section className="profile my-5">
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-11 col-lg-10 main_info py-4">
                        <div className="d-flex align-items-center heading justify-content-between">
                            <p>Your Profile</p>
                            <button className="float-right" onClick={handleClick} disabled={!edit}>Edit Profile</button>
                        </div>
                        <hr/>
                        <div className="row align-items-center mb-3">
                            <div className="col-lg-2 mb-3">
                                <div className="profile-img">
                                    <img src="https://cdn2.vectorstock.com/i/1000x1000/50/61/cartoon-business-man-cartoon-character-young-male-vector-15325061.jpg"/>
                                </div>
                            </div>
                            <div className="col-lg-5 mb-4">
                                <div>
                                    <label className="form-label">First Name</label>
                                    <input type="text" placeholder="Your First Name" onChange={handleChange} value={firstName} name="firstName" className="form-control"/>
                                </div>
                            </div>
                            <div className="col-lg-5 mb-4">
                                <div>
                                    <label className="form-label">Last Name</label>
                                    <input type="text" placeholder="Your Last Name" onChange={handleChange} value={lastName} name="lastName" className="form-control"/>
                                </div>
                            </div>
                            <div className="col-lg-2"></div>
                            <div className="col-lg-5 mb-4">
                                <div>
                                    <label className="form-label">House Number</label>
                                    <input type="text" placeholder="House Number" onChange={handleChange} value={HouseNo} name="HouseNo" className="form-control"/>
                                </div>
                            </div>
                            <div className="col-lg-5 mb-4">
                                <div>
                                    <label className="form-label">Land Mark</label>
                                    <input type="text" placeholder="Land Mark" onChange={handleChange} value={LandMark} name="LandMark" className="form-control"/>
                                </div>
                            </div>
                            <div className="col-lg-2"></div>
                            <div className="col-lg-5 mb-4">
                                <div>
                                    <label className="form-label">City</label>
                                    <input type="text" placeholder="City"  onChange={handleChange} value={City} name="City" className="form-control"/>
                                </div>
                            </div>
                            <div className="col-lg-5 mb-4">
                                <div>
                                    <label className="form-label">State</label>
                                    <input type="text" placeholder="State" onChange={handleChange} value={State} name="State" className="form-control"/>
                                </div>
                            </div>
                            <div className="col-lg-2"></div>
                            <div className="col-lg-5 mb-4">
                                <div>
                                    <label className="form-label">PinCode</label>
                                    <input type="text" placeholder="PinCode" onChange={handleChange} value={Pin} name="Pin" className="form-control"/>
                                </div>
                            </div>
                            <div className="col-lg-5 mb-4">
                                <div>
                                    <label className="form-label">Phone Number</label>
                                    <input type="text" placeholder="Phone Number" onChange={handleChange} value={phoneNumber} name="phoneNumber" className="form-control"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
       </section>
       <Footer/>
        </>
    )
}

export default Test