import React, {useState} from 'react'
import {Link, useHistory} from 'react-router-dom'
import {HeartOutlined,AlignRightOutlined, DashboardOutlined, LogoutOutlined, ProfileOutlined, UserOutlined, DatabaseOutlined} from '@ant-design/icons'
import { Badge, Dropdown, Menu} from 'antd'
import {useSelector, useDispatch} from 'react-redux'
import RegisterForm from '../RegisterForm'
import './Navbar.css'
import firebase from 'firebase'
import { toast } from 'react-toastify'
import Logo from '../images/logof.png'
import MobNav from './MobNav'

const Navbar = ()=>{
	const [isModalVisible, setIsModalVisible] = useState(false)
	const {user, cart} = useSelector((state)=>({...state}))
	const dispatch = useDispatch()
	const history = useHistory()

	const logout = async()=>{
		await firebase.auth().signOut()
		dispatch({
			type:'LOG_OUT_USER',
			payload:null
		})
		history.push('/login')
		toast.success('Logged You Out!!')
	}
	const menu = (
		<Menu>
		  <Menu.Item>
		  	<Link to="/wishlist"><HeartOutlined/> Wishlist</Link>
		  </Menu.Item>
		  <Menu.Item>
		  	<Link to="/user/order"><DatabaseOutlined/> Order</Link>
		  </Menu.Item>
		  {user && user.role === 'admin' ? (
			<Menu.Item>
				<Link to="/admin/dashboard"><DashboardOutlined/> DashBoard</Link>
			</Menu.Item>
		  ):(
			<Menu.Item>
				<Link to="/user/dashboard"><ProfileOutlined/> Profile</Link>
			</Menu.Item>
		  )}
		  <Menu.Item>
			<p onClick={logout}><LogoutOutlined/> Logout</p>
		  </Menu.Item>
		</Menu>
	  );

	  const sideBar=()=>{
		let sidenav = document.querySelector('.sidenav')
		sidenav.classList.toggle('open-sidenav')
	  }
    return(
        <>
           <nav className="navbar navbar-expand-md navbar-light bg-white">
				<Link to="/" className="navbar-brand logo"><img style={{width:'120px'}} alt="Logo" src={Logo}/></Link>
				<button className="navbar-toggler" onClick={sideBar}>
					<span className="navbar_menu"><AlignRightOutlined/></span>
				</button>
				<div className="collapse navbar-collapse" id="collapsible">
					<ul className="navbar-nav mx-auto">
						<li className="nav-item">
							<Link to="/" className="nav-link nav-active">Home</Link>
						</li>
						<li className="nav-item">
                            <Link to="/shop" className="nav-link ">Shop</Link>
						</li>
						<li className="nav-item">
                            <Link to="/blog" className="nav-link ">Blog</Link>
						</li>
						<li className="nav-item">
                            <Link to="/contact" className="nav-link ">Contact</Link>
						</li>
						<li className="nav-item">
                            <Link to="/cart" className="nav-link ">Cart <Badge count={cart && cart.length}></Badge></Link>
						</li>
					</ul>
                    <ul className="navbar-nav mx-auto mx-md-0 ml-md-auto">
						{user && user.token ? (
							<>
							<li className="nav-item">
								<Dropdown overlay={menu}>
									<p><UserOutlined/> {user.email.split('@')[0]}</p>
									
								</Dropdown>
							</li>
							</>) : (
							<>
								<li className="nav-item">
									<Link to='/login' className="nav-link nav-login">Login</Link>
								</li>
								<li className="nav-item" onClick={()=>setIsModalVisible(true)}>
									<p className="nav-link nav-register">Register</p>
								</li>
							</>
						)}
                    </ul>
				</div>
			</nav>
			<MobNav logout={logout} setIsModalVisible={setIsModalVisible}/>
			<RegisterForm isModalVisible={isModalVisible} setIsModalVisible={setIsModalVisible}/>
        </>
    )
}

export default Navbar