import React, {useState, useEffect} from 'react'
import {useDispatch, useSelector} from 'react-redux'
import {Input} from 'antd'
import { listUserAddress, userAddress } from '../../functions/checkOut'
import Footer from '../../components/home/Footer'

const Address = ({history})=>{
    const initialState = {
        firstName:'',
        lastName:'',
        phoneNumber:'',
        HouseNo:'',
        LandMark:'',
        City:'',
        Pin:'',
        State:''
    }
    const [address, setAddress] = useState(initialState)
    let {firstName, lastName, phoneNumber, HouseNo, LandMark,City, State, Pin} = address
    const {user} = useSelector(state => ({...state}))

    useEffect(()=>{
        listUserAddress(user && user.token).then((res)=>{
            if(res.data.address.firstName){
                setAddress(res.data.address)
            }else{
                setAddress(initialState)
            }
        })
    }, [])

    const handleChange=(e)=>{
        setAddress({...address, [e.target.name]:e.target.value})
    }
    const handleClick = ()=>{
        userAddress(user.token, address).then((res)=>{
            if(res.data.success){
                history.push('/checkout')
            }
        }).catch((err)=>{
            console.log(err)
        })
    }
    return(
        <>
            <div className="container py-5">
                <div className="row">
                    <div className="col-11 mx-auto">
                        <h4 className="text-center">Billing Address</h4>
                        <div className="head-underline mx-auto mb-3"></div>
                        <div className="d-flex">
                            <Input type="text" placeholder='First Name' value={firstName} name='firstName' onChange={handleChange} className="mt-2 mr-3"/>
                            <Input type="text" placeholder='Last Name' value={lastName} name='lastName' onChange={handleChange} className="mt-2"/>
                        </div>
                        <Input type="email" placeholder='Email' value={user && user.email} className="mt-3"/>
                        <Input type="number" placeholder='Phone Number' value={phoneNumber} name='phoneNumber' onChange={handleChange} className="mt-3"/>
                        <Input type="text" placeholder='House No' value={HouseNo} name='HouseNo' onChange={handleChange} className="mt-3"/>
                        <Input type="text" placeholder='Land Mark' value={LandMark} name='LandMark' onChange={handleChange} className="mt-3"/>
                        <Input type="text" placeholder='City' value={City} name='City' onChange={handleChange} className="mt-3"/>
                        <Input type="text" placeholder='Pin' value={Pin} name='Pin' onChange={handleChange} className="mt-3"/>
                        <Input type="text" placeholder='State' value={State} name='State' onChange={handleChange} className="mt-3"/>

                        <button className="address-btn mt-4" onClick={handleClick}>Submit</button>
                    </div>
                </div>
            </div>
            <Footer/>
        </>
    )
}

export default Address