import { Card, Tooltip } from 'antd'
import React, {useEffect, useState} from 'react'
import {Link} from 'react-router-dom'
import AdminNav from '../../components/nav/AdminNav'
import { listAllBlogs, removeBlog } from '../../functions/blog'
import {EditOutlined, DeleteOutlined, ReloadOutlined, PlusCircleOutlined, CheckCircleOutlined} from '@ant-design/icons'
import {useSelector} from 'react-redux'
import { toast } from 'react-toastify'
import CreateBlogModal from '../../components/modals/CreateBlogModal'

const Blogs = ()=>{
    const [blogs, setBlogs] = useState([])
    const [isModalVisible, setIsModalVisible] = useState(false)
    const {Meta} = Card
    let {user} = useSelector((state)=>({...state}))
    useEffect(()=>{
        loadAllBlogs()
    }, [])

    const loadAllBlogs = ()=>{
        listAllBlogs(0).then((res)=>{
            console.log(res.data)
            setBlogs(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleDelete = (slug, title)=>{
        removeBlog(user.token, slug).then((res)=>{
            console.log(res.data)
            toast.success(res.data.success)
            loadAllBlogs()
        }).catch((err)=>{
            toast.error(`${title} was not deleted....Try Again!!`)
        })
    }
    return(
        <>
         <div className="container-fluid">
            <div className="row">
                <AdminNav/>
                <div className="col-10 pl-2 pr-1 py-5 bg-gray  dash-content">
                    <Card className="card_table">
                    <div className="table-heading d-flex align-items-center justify-content-between">
                        <p className="pt-3 pl-3 text-white">BLOGS</p>
                        <div className=" d-flex  align-items-center justify-content-end">
                        <Tooltip title='Create New Product'><PlusCircleOutlined className=" mr-2 plus_icon" onClick={()=>setIsModalVisible(true)}/></Tooltip>
                        <Tooltip title="Refresh"><ReloadOutlined className="mr-2 plus_icon" onClick={()=>loadAllBlogs()}/></Tooltip>
                        </div>
                    </div>
                    <CreateBlogModal isModalVisible={isModalVisible} loadAllBlogs={loadAllBlogs} setIsModalVisible={setIsModalVisible}/>
                    <table className="table table-responsive-sm">
                        <thead className="thead-table">
                            <tr>
                                <th scope="col">Title</th>
                                <th scope="col">Description</th>
                                <th scope="col">Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {blogs && blogs.map((item)=>{
                                let {title, description, slug} = item
                                return(
                                    <>
                                        <tr className="table-body-row">
                                            <td >{title}</td>
                                            <td >{`${description.slice(0,50)}....`}</td>
                                            <td><Tooltip title={'Delete'}><DeleteOutlined className="text-danger pr-2" onClick={()=>handleDelete(slug, title)}/></Tooltip> 
                                                <Tooltip title={"Edit"}><Link to={`/admin/update/blog/${slug}`}><EditOutlined className="text-warning"/></Link></Tooltip></td>
                                        </tr>
                                    </>
                                )
                            })}
                        </tbody>
                    </table>
                    </Card>
                </div>
            </div>  
        </div>
        </>
    )
}

export default Blogs