import React from 'react'
import {useDispatch, useSelector} from 'react-redux'
import {Drawer, Button} from 'antd'
import {useHistory} from 'react-router-dom'

const SideDrawer = ()=>{
    const dispatch = useDispatch()
    const history = useHistory()
    const {cart, drawer} = useSelector((state)=>({...state}))

    const handleClose = ()=>{
        dispatch({
            type:'SET_VISIBILITY',
            payload:false
        })
    }

    const handleClick = ()=>{
        dispatch({
            type:'SET_VISIBILITY',
            payload:false
        })
        history.push('/cart')
    }
    return(
        <>
            <Drawer className="text-center" closable={true} visible={drawer} placement='right' title={`Cart/${cart && cart.length}`} onClose={handleClose}>
                <div className="row">
                    {cart && cart.map((item)=>{
                        return(
                            <>
                                <div className="col mx-auto my-3">
                                    <img  style={{height:'120px', width:'auto'}} src={item.images.length !== 0 ? (item.images[0].url):'Image'}/><br/>
                                    <p className="badge badge-warning mt-2">{`${item.title} x ${item.count}`}</p>
                                </div>
                            </>
                        )
                    })}
                </div>
                <Button shape="round" block type="primary" onClick={handleClick}>Go To Cart</Button>
            </Drawer>
        </>
    )
}
export default SideDrawer