import React,{useState,  useEffect} from 'react'
import {useSelector, useDispatch} from 'react-redux'
import { RatingAvg } from '../../functions/ratings'
import {Link} from 'react-router-dom'
import { listWishlist, removeWishlist } from '../../functions/user'
import './Wishlist.css'
import _ from 'lodash'
import Footer from '../../components/home/Footer'

const Wishlist = ()=>{
    const [products, setProducts] = useState([])
    const {user, drawer} = useSelector((state)=>({...state}))
    const dispatch = useDispatch()
    useEffect(() => {
        listWishlistFunc()
    }, [user])

    const listWishlistFunc = ()=>{
        listWishlist(user && user.token).then((res)=>{
            console.log(res.data)
            setProducts(res.data.wishlist)
        })
    }

    const handleAddToCart=(product)=>{
        let productWithCount = {...product, count:1}
        let cart = []
        if(localStorage.getItem('cart')){
            cart = JSON.parse(localStorage.getItem('cart'))
        }
        cart.push(productWithCount)
        let unique = _.uniqWith(cart, _.isEqual)
        localStorage.setItem('cart', JSON.stringify(unique))

        dispatch({
            type:'ADD_TO_CART',
            payload:unique
        })

        dispatch({
            type:'SET_VISIBILITY',
            payload:true
        })
        removeFromWishlist(product._id)
    }

    const removeFromWishlist=(id)=>{
        removeWishlist(user && user.token, id).then((res)=>{
            listWishlistFunc()
        }).catch((err)=>{
            console.log(err)
        })
    }
    return(
        <>
        <section id="wishlist" className="my-4">
            <div className="container py-5">
                <div className="row">
                    {products && products.length > 0 ? (
                        <>
                        {products && products.map((item)=>{
                            let {images, title, ratings, price} = item
                            return(
                                <>
                                <div className="col-12 col-lg-4 mx-auto my-3">
                                    <div className="product_img_wish_container">
                                        <img src={images[0].url}/>
                                    </div>
                                    <div className="wishlist_content text-center">
                                        <p className="title">{title}</p>
                                        {/* {RatingAvg(ratings)} */}
                                        <p className="amount"><i className="fa fa-rupee"></i> {price}</p><br/>
                                        <div className="d-flex align-items-center w-100 justify-content-center">
                                            <button className="add-to-cart-btn" onClick={()=>handleAddToCart(item)}>Add To Cart</button>
                                            <button className="add-to-wishlist-btn mx-3" onClick={()=>removeFromWishlist(item._id)}>Remove</button>
                                        </div>
                                    </div>
                                </div>
                                </>
                            )
                        })}
                        </>
                    ) : (
                        <>
                            <div className="empty-wish">
                                <i className="fa fa-heart-o"></i>
                                <h6>wishlist is empty</h6>
                                <p>You don't have any products in the wishlist yet.<br/>You will find a lot of interesting products on our "Shop" page.</p>
                                <Link className="wish-btn" to="/shop">Return To Shop</Link>
                            </div>
                        </>
                    )}
                    
                </div>
            </div>
        </section>
        <Footer/>
        </>
    )
}

export default Wishlist