import React, {useState, useEffect} from 'react'
import './HomeProduct.css'
import {Link} from 'react-router-dom'
import _ from 'lodash'
import {useDispatch} from 'react-redux'
import { listProducts } from '../../functions/product'
import {RatingAvg} from '../../functions/ratings'
import { Tooltip } from 'antd'
import SwiperCore, { Navigation, Pagination, Scrollbar, Autoplay } from 'swiper';
import {Swiper, SwiperSlide} from 'swiper/react'

SwiperCore.use([Navigation, Pagination, Scrollbar, Autoplay]);

const HomeProduct = ({criteria, title})=>{
    const [product, setProduct] = useState('')

    useEffect(()=>{
        listProducts(criteria, 'asc', 1).then((res)=>{setProduct(res.data)})
    }, [])
    const dispatch = useDispatch()
    const handleAddToCart = (product)=>{
        let cart = []
        if(localStorage.getItem('cart')){
            cart = JSON.parse(localStorage.getItem('cart'))
        }
        cart.push({...product, count:1})
        let unique = _.uniqWith(cart, _.isEqual)
        localStorage.setItem('cart', JSON.stringify(unique))
        dispatch({
            type:'ADD_TO_CART',
            payload:unique
        })

        dispatch({
            type:'SET_VISIBILITY',
            payload:true
        })
    }

    const breakpoints = {
        640:{
            slidesPerView:1
        },
        768:{
            slidesPerView:2
        },
        1024:{
            slidesPerView:4
        }
    }
    return(
        <>
        <section className="products bg-white my-5 py-4">
            <div className="container-fluid pl-lg-5">
                <h5 className="text-green">{title}</h5>
                <hr/>
            </div>
            <div className="container-fluid">
                <Swiper loop breakpoints={breakpoints}  autoplay={{delay:2000}}>
                    {product && product.map((item)=>{
                        let {images, title, price,slug,ratings} = item
                        return(
                        <>
                        <SwiperSlide>
                        <div className="col-12 my-2 mx-auto related" data-aos="fade-up" data-aos-duration="1000">
                            <div className="shop_product_container">
                                <div className="shop_product_img_container">
                                    <img src={images && images[0].url}/>
                                    <Tooltip placement="left" title="Add To Cart">
                                        <i className="fa fa-shopping-cart" onClick={()=>handleAddToCart(item)}></i>
                                    </Tooltip>
                                    <Link to={`/product/${slug}`}>
                                        <Tooltip placement="left" title="Quick View">
                                            <i className="fa fa-search-plus "></i>
                                        </Tooltip>
                                    </Link>
                                </div>
                                <div className="related_product_body">
                                    <h5 className="pt-3"><Link className="text-dark" to={`/product/${slug}`}>{title}</Link></h5>
                                    {RatingAvg(ratings)}
                                    <h6 className="pt-2"><i className="fa fa-rupee"></i> {price}</h6>
                                </div>
                            </div>
                        </div>
                        </SwiperSlide>
                        </>
                        )
                    })}
                </Swiper>
            </div>
        </section>
        </>
    )
}

export default HomeProduct