import axios from 'axios'

const {REACT_APP_API_URL} = process.env

export const createOrUpdateUser = async(authtoken)=>{
    return await axios.post(`${REACT_APP_API_URL}/create-or-update-user`, {},{
        headers:{
            authtoken
        }
    })
}

export const currentUser = async(authtoken)=>{
    return await axios.post(`${REACT_APP_API_URL}/current-user`, {},{
        headers:{
            authtoken
        }
    })
}
