import axios from 'axios'
const {REACT_APP_API_URL} = process.env

export const listProducts = async(sort, order, page)=>{
    return await axios.post(`${REACT_APP_API_URL}/products`,{sort, order, page} )
}

export const listProductsCount= async()=>{
    return await axios.get(`${REACT_APP_API_URL}/products/count`)
}
export const readProduct = async(slug)=>{
    return await axios.get(`${REACT_APP_API_URL}/product/${slug}`)
}

export const createProduct = async(authtoken, body)=>{
    return await axios.post(`${REACT_APP_API_URL}/product`, body, {
        headers:{authtoken}
    })
}

export const updateProduct = async(authtoken, slug, body)=>{
    return await axios.put(`${REACT_APP_API_URL}/product/update/${slug}`, body, {
        headers:{authtoken}
    })
}

export const removeProduct = async(authtoken, slug)=>{
    return await axios.delete(`${REACT_APP_API_URL}/product/${slug}`, {
        headers:{authtoken}
    })
}

export const listRelatedProducts = async(productId)=>{
    return await axios.get(`${REACT_APP_API_URL}/product/related/${productId}`)
}

export const filterProducts = async(query)=>{
    return await axios.post(`${REACT_APP_API_URL}/product/filters`,query)
}

export const rateProduct = async(authtoken,id, star, comment)=>{
    return await axios.post(`${REACT_APP_API_URL}/product/rate/${id}`,{star, comment}, {
        headers:{authtoken}
    })
}
