import React, {useState, useEffect} from 'react'
import { useSelector } from 'react-redux'
import { readSub, updateSub } from '../../functions/sub'
import {Input, Button, Select} from 'antd'
import AdminNav from '../../components/nav/AdminNav'
import { listCategories } from '../../functions/category'
import { toast } from 'react-toastify'

const UpdateSub = ({match, history})=>{
    const [name, setName] = useState('')
    const [origName, setOrigName] = useState('')
    const [categories, setCategories] = useState('')
    const [category, setCategory] = useState('')
    const {user} = useSelector((state)=>({...state}))
    const {Option} = Select

    useEffect(()=>{
        loadTheSub()
        loadAllCategories()
    }, [])

    const loadAllCategories = ()=>{
        listCategories().then((res)=>{
            setCategories(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const loadTheSub = ()=>{
        readSub(match.params.slug).then((res)=>{
            let {name, category} = res.data
            setName(name)
            setOrigName(name)
            setCategory(category)
        }).catch((err)=>{
            console.log(err)
        })
    }

    const handleSubmit = ()=>{
        updateSub(user.token, {name, category}, match.params.slug).then((res)=>{
            toast.success(`${origName} is successfully updated to ${res.data.name}`)
            history.push('/admin/sub')
        }).catch((err)=>{
            toast.error(`${origName} was not updated...Try Again!!`)
        })
    }
    return(
        <>
        <div className="container-fluid">
            <div className="row">
                <AdminNav/>
                <div className="col-10 mx-auto bg-gray dash-content text-center">
                    <h4 className="text-center pt-5 pb-3">Update Sub Category</h4>
                    <Select placeholder="Choose The Category" value={category} className="mb-3 col-10 col-lg-6" onChange={(e)=>setCategory(e)}>
                        {categories && categories.map((item)=>{
                            return(
                                <>
                                    <Option key={item.slug} value={item._id}>{item.name}</Option>
                                </>
                            )
                        })}
                    </Select><br/>
                    <Input type="text" placeholder="Sub Name" value={name} onChange={(e)=>setName(e.target.value)} className="col-10 col-lg-6"/><br/>
                    <Button type="primary" shape="round" block className="col-10 col-lg-6 my-3" onClick={handleSubmit}>Submit</Button>
                </div>
            </div>  
        </div>
        </>
    )
}

export default UpdateSub