import axios from 'axios'
const {REACT_APP_API_URL} = process.env

export const getAllOrders = async(authtoken, sort)=>{
    return await axios.post(`${REACT_APP_API_URL}/orders`,{sort},{
        headers:{authtoken}
    })
}

export const readOrder = async(authtoken, id)=>{
    return await axios.get(`${REACT_APP_API_URL}/order/${id}`,{
        headers:{authtoken}
    })
}

export const updateOrder = async(authtoken, status, id)=>{
    return await axios.put(`${REACT_APP_API_URL}/order/update/${id}`,{status},{
        headers:{authtoken}
    })
}

export const getUserOrders = async(authtoken)=>{
    return await axios.get(`${REACT_APP_API_URL}/user/orders`,{
        headers:{authtoken}
    })
}
