import axios from 'axios'
const {REACT_APP_API_URL} = process.env

export const listSubs = async()=>{
    return await axios.get(`${REACT_APP_API_URL}/subs`)
}

export const readSub = async(slug)=>{
    return await axios.get(`${REACT_APP_API_URL}/sub/${slug}`)
}

export const createSub = async(authtoken, body)=>{
    return await axios.post(`${REACT_APP_API_URL}/sub`, body, {
        headers:{authtoken}
    })
}

export const updateSub = async(authtoken, body, slug)=>{
    return await axios.put(`${REACT_APP_API_URL}/sub/update/${slug}`, body, {
        headers:{
            authtoken
        }
    })
}

export const removeSub = async(authtoken, slug)=>{
    return await axios.delete(`${REACT_APP_API_URL}/sub/${slug}`, {
        headers:{authtoken}
    })
}