import React from 'react'
import { Document, Page, View, Text, StyleSheet, Image, PDFDownloadLink} from '@react-pdf/renderer'
import Logo from '../images/logof.png'
import InvoiceNo from './InvoiceNo';
import BillTo from './BillTo';
import InvoiceTableHeader from './InvoiceTableHeader';
import InvoiceFooter from './InvoiceFooter';

const Invoice = ({data})=>{
    const styles = StyleSheet.create({
        page: {
            fontFamily: 'Helvetica',
            fontSize: 11,
            paddingTop: 30,
            paddingLeft:60,
            paddingRight:60,
            lineHeight: 1.5,
            flexDirection: 'column',
        }, 
        logo: {
            width: 150,
            height: 66,
            marginLeft: 'auto',
            marginRight: 'auto'
        }
      });
    return(
        <>
                <Document>
                    <Page style={styles.page} size="A4">
                        <Image src={Logo} style={styles.logo}></Image>
                        <InvoiceNo paymentInfo={data && data[0].paymentIntent.order_id} orderDate={data && data[0].createdAt.split('T')[0]}/>
                        <BillTo address = {data && data[0].orderedBy.address}/>
                        <InvoiceTableHeader 
                            product={data && data[0].products} 
                            amount={data && data[0].paymentIntent.amount} 
                            method={data && data[0].paymentIntent.payment_method}
                            status={data && data[0].paymentStatus}
                        />
                        <InvoiceFooter/>
                    </Page>
                </Document>
        </>
    )
}

export default Invoice