import StarRatings from "react-star-ratings"

export const RatingAvg = (p, place)=>{
    if(p && p.length >0){
        let ratings = p
        let totalRating = ratings.length
        let starArr = []
        ratings.map((item)=>{
            starArr.push(item.star)
        })
        let totalReduced = starArr.reduce((p, n)=>p+n, 0)
        let highest = totalRating * 5
        let result = (totalReduced * 5) / highest

        const ratingDesign=()=>{
            if(place == 'shop'){
                return(
                    <>
                        <p className="badge badge-success px-2">{result} <i className="fa fa-star text-white"></i></p> 
                        <span className="pl-2 text-muted">({p.length})</span>
                    </>
                )
            }else{
                return (
                    <span>
                        <StarRatings starDimension="20px" starSpacing="1px" numberOfStars={5} starRatedColor="red" rating={result} isSelectable={false}/>
                    </span>
                )
            }
        }
        return ratingDesign()
        
    }else{
        return (
            <span>
                <StarRatings starDimension="20px" starSpacing="1px" numberOfStars={5} starRatedColor="red" rating={0} isSelectable={false}/>
            </span>
        )
    }
}

