import { CheckCircleOutlined, CloseCircleOutlined, DeleteOutlined, EditOutlined, PlusCircleOutlined, ReloadOutlined } from '@ant-design/icons';
import React, {useState, useEffect} from 'react'
import {useSelector} from 'react-redux'
import AdminNav from '../../components/nav/AdminNav';
import { listProducts, removeProduct } from '../../functions/product';
import {Card, Tooltip} from 'antd'
import {Link} from 'react-router-dom'
import './Products.css'
import { toast } from 'react-toastify';
import CreateProductModal from '../../components/modals/CreateProductModal';

const Products = ()=>{
    const [products, setProducts] = useState([])
    const [isModalVisible, setIsModalVisible] = useState(false)
    const {user} = useSelector((state)=>({...state}))
    useEffect(()=>{
        loadAllProducts()
    }, [])

    const loadAllProducts = ()=>{
        listProducts('createdAt', 'desc', 1).then((res)=>{
            setProducts(res.data)
        }).catch((err)=>{
            console.log(err)
        })
    }
    const handleDelete=(slug, title)=>{
        removeProduct(user.token, slug).then((res)=>{
            toast.success(`${title} is successfully deleted!!`)
            loadAllProducts()
        }).catch((err)=>{
            toast.error(`${title} is not deleted...Try Again!`)
        })
    }
    return(
        <>
        <div className="container-fluid">
            <div className="row">
                <AdminNav/>
                <div className="col-10 pl-2 pl-lg-4 pr-1 pr-lg-4 py-5 bg-gray  dash-content">
                    <Card className="card_table">
                    <div className="table-heading d-flex align-items-center justify-content-between">
                        <p className="pt-3 pl-3 text-white">PRODUCTS</p>
                        <div className=" d-flex  align-items-center justify-content-end">
                        <Tooltip title='Create New Product'><PlusCircleOutlined className=" mr-2 plus_icon" onClick={()=>setIsModalVisible(true)}/></Tooltip>
                        <Tooltip title="Refresh"><ReloadOutlined className="mr-2 plus_icon" onClick={()=>loadAllProducts()}/></Tooltip>
                        </div>
                    </div>
                    <CreateProductModal isModalVisible={isModalVisible} loadAllProducts={loadAllProducts} setProducts={setProducts} setIsModalVisible={setIsModalVisible}/>
                    <table className="table table-responsive-sm">
                        <thead className="thead-table">
                            <tr>
                                <th scope="col">Title</th>
                                <th scope="col">Price</th>
                                <th scope="col">Quantity</th>
                                <th scope="col">Sold</th>
                                <th scope="col">Shipping</th>
                                <th scope="col">Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {products && products.map((item)=>{
                                let {title, price, quantity, sold, shipping, slug} = item
                                return(
                                    <>
                                        <tr className="table-body-row">
                                            <td >{title}</td>
                                            <td >{price}</td>
                                            <td >{quantity}</td>
                                            <td >{sold}</td>
                                            <td>{shipping == 'Yes' ? <CheckCircleOutlined className="text-success"/> : <CloseCircleOutlined className="text-danger"/>}</td>
                                            <td><Tooltip title={'Delete'}><DeleteOutlined className="text-danger pr-2" onClick={()=>handleDelete(slug, title)}/></Tooltip> 
                                                <Tooltip title={"Edit"}><Link to={`/admin/update/product/${slug}`}><EditOutlined className="text-warning"/></Link></Tooltip></td>
                                        </tr>
                                    </>
                                )
                            })}
                        </tbody>
                    </table>
                    </Card>
                </div>
            </div>  
        </div>
        </>
    )
}

export default Products;